import {DataUrlSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/DataUrl';
import {ParamsLabelRegister} from '../BaseSetup';

export const dataUrlSopParamsLabelRegister: ParamsLabelRegister<typeof DataUrlSopNode, DataUrlSopNode> = {
	nodeClass: DataUrlSopNode,
	setupFunc: (node) => ({
		params: node.p.url,
		callback: () => {
			const url = node.p.url.rawInput();
			if (url) {
				const elements = url.split('/');
				return elements[elements.length - 1];
			} else {
				return '';
			}
		},
	}),
};
