import {JsConnectionPointType} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';
import type {PhysicsDebugSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PhysicsDebug';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

function onCreateHook(node: PhysicsDebugSopNode) {
	const physicsDebugUpdate = node.createNode('physicsDebugUpdate');
	const onTick = node.createNode('onTick');

	physicsDebugUpdate.setInput(JsConnectionPointType.TRIGGER, onTick);

	onTick.uiData.setPosition(-100, 100);
	physicsDebugUpdate.uiData.setPosition(100, 100);
}

export class PhysicsDebugSopOnCreateRegister extends SopOnCreateHookRegister<SopType.PHYSICS_DEBUG> {
	override type(): SopType.PHYSICS_DEBUG {
		return SopType.PHYSICS_DEBUG;
	}
	override onCreate(node: PhysicsDebugSopNode) {
		onCreateHook(node);
	}
}
