import {DragEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/Drag';
import {ParamsLabelRegister} from '../BaseSetup';

export const dragEventParamsLabelRegister: ParamsLabelRegister<typeof DragEventNode, DragEventNode> = {
	nodeClass: DragEventNode,
	setupFunc: (node) => {
		const params = [node.p.dragover];
		return {
			params,
			callback: () =>
				params
					.map((p) => {
						return p.value ? p.name() : undefined;
					})
					.filter((v) => v)
					.join(', '),
		};
	},
};
