import {InstanceVariable} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/code/assemblers/instanceBuilder/InstanceBuilderAssemblerCommon';
import type {InstanceBuilderSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/InstanceBuilder';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

function onCreateHook(node: InstanceBuilderSopNode) {
	const globals = node.createNode('globals');
	const output1 = node.createNode('output');
	const add1 = node.createNode('add');

	add1.setInput(0, globals, InstanceVariable.INSTANCE_POSITION);
	output1.setInput(InstanceVariable.INSTANCE_POSITION, add1);

	// add1.p.radius.set(0.4);

	globals.uiData.setPosition(-400, 0);
	output1.uiData.setPosition(0, 0);
	add1.uiData.setPosition(-200, 0);
}

export class InstanceBuilderSopOnCreateRegister extends SopOnCreateHookRegister<SopType.INSTANCE_BUILDER> {
	override type(): SopType.INSTANCE_BUILDER {
		return SopType.INSTANCE_BUILDER;
	}
	override onCreate(node: InstanceBuilderSopNode) {
		onCreateHook(node);
	}
}
