import {JsConnectionPointType} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';
import type {PhysicsPlayerSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PhysicsPlayer';
import {CORE_PLAYER_INPUTS} from '../../../../../../@polygonjs/polygonjs/src/core/player/PlayerCommon';
import type {GeoObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/Geo';
import {CoreString} from './../../../../../../@polygonjs/polygonjs/src/core/String';
import {TransformSopNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Transform';
import {CoreWalker} from './../../../../../../@polygonjs/polygonjs/src/core/Walker';
import {TransformTargetType} from './../../../../../../@polygonjs/polygonjs/src/core/Transform';
import {MeshStandardBuilderMatNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshStandardBuilder';
import {MaterialsNetworkSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/MaterialsNetwork';
import {ParamType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/ParamType';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

const SPHERE_NODE_NAME = 'sphere_PLAYER';
const TRANSFORM_NODE_NAME = 'transform_PLAYER';
const CAMERA_NODE_NAME = 'perspectiveCamera_PLAYER';
const CAMERA_CONTROLS_NODE_NAME = 'cameraControls_PLAYER';

export function physicsPlayerSopOnCreate(node: PhysicsPlayerSopNode) {
	const physicsPlayerUpdate = node.createNode('physicsPlayerUpdate');
	const onTick = node.createNode('onTick');
	const setPlayerInput = node.createNode('setPlayerInput');
	const onKey = node.createNode('onKey');

	physicsPlayerUpdate.setInput(JsConnectionPointType.TRIGGER, onTick);

	for (let corePlayerInput of CORE_PLAYER_INPUTS) {
		physicsPlayerUpdate.setInput(corePlayerInput, setPlayerInput, corePlayerInput);
	}
	setPlayerInput.setInput(JsConnectionPointType.TRIGGER, onKey);
	onKey.p.keyCodes.set('Space ShiftLeft ShiftRight KeyW KeyA KeyS KeyD ArrowUp ArrowLeft ArrowDown ArrowRight');

	physicsPlayerUpdate.uiData.setPosition(100, 0);
	onTick.uiData.setPosition(-200, 0);
	setPlayerInput.uiData.setPosition(-200, 200);
	onKey.uiData.setPosition(-600, 200);

	// create camera and controls
	const geoNode = node.parent() as GeoObjNode;

	// function _findCameraNode() {
	// 	return geoNode
	// 		.nodesByType('perspectiveCamera')
	// 		.filter((cameraNode) => cameraNode.name() == CAMERA_NODE_NAME)[0];
	// }
	function _createSphereNodes() {
		function _createSphereNode() {
			const sphere1 = geoNode.createNode('sphere');
			sphere1.p.radius.set(0.5);
			sphere1.setName(`${SPHERE_NODE_NAME}${CoreString.tailDigits(node.name())}`);
			sphere1.uiData.setPosition(node.uiData.position().x - 100, node.uiData.position().y - 600);
			return sphere1;
		}
		function _createMaterialNode() {
			const MAT_NAME = 'MAT';
			const MESH_STANDARD_MAT_NAME = 'meshStandardBuilder_PLAYER';
			function createMatNetwork(geoNode: GeoObjNode) {
				const MAT = geoNode.createNode('materialsNetwork');
				MAT.setName(MAT_NAME);

				const nodePos = node.uiData.position();
				MAT.uiData.setPosition(nodePos.x - 200, nodePos.y);

				return MAT;
			}

			const MAT = geoNode.nodesByType('materialsNetwork')[0] || createMatNetwork(geoNode);

			function createMeshMaterial(MAT: MaterialsNetworkSopNode) {
				// ensure we don't link to a CopNetwork for instance
				const materialNode = MAT.createNode('meshStandardBuilder');

				function create_globals1(parentNode: MeshStandardBuilderMatNode) {
					var globals1 = parentNode.createNode('globals');
					globals1.setName('globals1');
					globals1.uiData.setPosition(-450, 0);
					return globals1;
				}
				function create_output1(parentNode: MeshStandardBuilderMatNode) {
					var output1 = parentNode.createNode('output');
					output1.setName('output1');
					output1.uiData.setPosition(400, 0);
					return output1;
				}
				function create_vec3ToFloat1(parentNode: MeshStandardBuilderMatNode) {
					var vec3ToFloat1 = parentNode.createNode('vec3ToFloat');
					vec3ToFloat1.setName('vec3ToFloat1');
					vec3ToFloat1.uiData.setPosition(-200, 200);
					return vec3ToFloat1;
				}
				function create_floatToVec2_1(parentNode: MeshStandardBuilderMatNode) {
					var floatToVec2_1 = parentNode.createNode('floatToVec2');
					floatToVec2_1.setName('floatToVec2_1');
					floatToVec2_1.uiData.setPosition(-100, 200);
					return floatToVec2_1;
				}
				function create_checkers1(parentNode: MeshStandardBuilderMatNode) {
					var checkers1 = parentNode.createNode('checkers');
					checkers1.setName('checkers1');
					checkers1.uiData.setPosition(50, 200);
					return checkers1;
				}
				function create_mix1(parentNode: MeshStandardBuilderMatNode) {
					var mix1 = parentNode.createNode('mix');
					mix1.setName('mix1');
					mix1.uiData.setPosition(250, 0);
					mix1.addParam(ParamType.VECTOR3, 'value0', [0, 0, 0], {spare: true, editable: false});
					mix1.addParam(ParamType.VECTOR3, 'value1', [0, 0, 0], {spare: true, editable: false});
					mix1.addParam(ParamType.FLOAT, 'blend', 0.5, {spare: true, editable: false});
					return mix1;
				}
				function create_constant1(parentNode: MeshStandardBuilderMatNode) {
					var constant1 = parentNode.createNode('constant');
					constant1.setName('constant1');
					constant1.uiData.setPosition(50, -150);
					constant1.p.type.set(4);
					constant1.p.color.set([0.8745098039215686, 0.6235294117647059, 0.7019607843137254]);
					constant1.p.asColor.set(true);
					return constant1;
				}
				function create_constant2(parentNode: MeshStandardBuilderMatNode) {
					var constant2 = parentNode.createNode('constant');
					constant2.setName('constant2');
					constant2.uiData.setPosition(0, 0);
					constant2.p.type.set(4);
					constant2.p.color.set([0.2549019607843137, 0.043137254901960784, 0.043137254901960784]);
					constant2.p.asColor.set(true);
					return constant2;
				}
				const globals1 = create_globals1(materialNode);
				const output1 = create_output1(materialNode);
				const vec3ToFloat1 = create_vec3ToFloat1(materialNode);
				const floatToVec2_1 = create_floatToVec2_1(materialNode);
				const checkers1 = create_checkers1(materialNode);
				const mix1 = create_mix1(materialNode);
				const constant1 = create_constant1(materialNode);
				const constant2 = create_constant2(materialNode);
				output1.setInput('color', mix1, 'mix');
				vec3ToFloat1.setInput('vec', globals1, 'position');
				floatToVec2_1.setInput('x', vec3ToFloat1, 'x');
				floatToVec2_1.setInput('y', vec3ToFloat1, 'z');
				checkers1.setInput('uv', floatToVec2_1, 'vec2');
				mix1.setInput('value0', constant1, 'val');
				mix1.setInput('value1', constant2, 'val');
				mix1.setInput('blend', checkers1, 'checker');

				materialNode.setName(MESH_STANDARD_MAT_NAME);

				return materialNode;
			}

			const material1 = geoNode.createNode('material');
			material1.uiData.setPosition(node.uiData.position().x - 100, node.uiData.position().y - 400);
			const matNode = MAT.node(MESH_STANDARD_MAT_NAME) || createMeshMaterial(MAT);
			material1.p.material.setNode(matNode, {relative: true});
			return material1;
		}
		function _createTransformNode() {
			const transform1 = geoNode.createNode('transform');
			transform1.p.t.y.set(3);
			transform1.setApplyOn(TransformTargetType.OBJECT);
			transform1.setName(`${TRANSFORM_NODE_NAME}${CoreString.tailDigits(node.name())}`);
			transform1.uiData.setPosition(node.uiData.position().x - 100, node.uiData.position().y - 200);
			return transform1;
		}
		const sphereNode = _createSphereNode();
		const materialNode = _createMaterialNode();
		const transformNode = _createTransformNode();
		materialNode.setInput(0, sphereNode);
		transformNode.setInput(0, materialNode);
		return {sphereNode, transformNode};
	}

	function _createCameraNodes(transformNode: TransformSopNode) {
		function _createCameraNode() {
			const cameraNode = geoNode.createNode('perspectiveCamera');
			cameraNode.p.position.set([5, 5, 5]);
			cameraNode.setName(`${CAMERA_NODE_NAME}${CoreString.tailDigits(node.name())}`);
			cameraNode.uiData.setPosition(node.uiData.position().x + 100, node.uiData.position().y - 400);
			return cameraNode;
		}
		// function _findCameraControlsNode() {
		// 	return geoNode
		// 		.nodesByType('cameraControls')
		// 		.filter((cameraNode) => cameraNode.name() == CAMERA_CONTROLS_NODE_NAME)[0];
		// }
		function _createCameraControlsNode() {
			const cameraControlsNode = geoNode.createNode('cameraControls');
			cameraControlsNode.setName(`${CAMERA_CONTROLS_NODE_NAME}${CoreString.tailDigits(node.name())}`);

			const cameraOrbitControlsNode = cameraControlsNode.createNode('cameraOrbitControls');
			cameraOrbitControlsNode.p.allowPan.set(false);
			cameraOrbitControlsNode.p.minDistance.set(2);
			const transformRelativePath = CoreWalker.relativePath(cameraOrbitControlsNode, transformNode);
			cameraOrbitControlsNode.p.target.set([
				`ch('${transformRelativePath}/tx')`,
				`ch('${transformRelativePath}/ty')`,
				`ch('${transformRelativePath}/tz')`,
			]);

			cameraControlsNode.p.node.setNode(cameraOrbitControlsNode, {relative: true});
			cameraControlsNode.uiData.setPosition(node.uiData.position().x + 100, node.uiData.position().y - 200);
			return cameraControlsNode;
		}
		const cameraNode = _createCameraNode();
		const cameraControlsNode = _createCameraControlsNode();
		cameraControlsNode.setInput(0, cameraNode);
		return {cameraNode, cameraControlsNode};
	}

	const {transformNode} = _createSphereNodes();
	const {cameraControlsNode} = _createCameraNodes(transformNode);
	node.setInput(0, transformNode);
	node.setInput(1, cameraControlsNode);
}

export class PhysicsPlayerSopOnCreateRegister extends SopOnCreateHookRegister<SopType.PHYSICS_PLAYER> {
	override type(): SopType.PHYSICS_PLAYER {
		return SopType.PHYSICS_PLAYER;
	}
	override onCreate(node: PhysicsPlayerSopNode) {
		physicsPlayerSopOnCreate(node);
	}
}
