import {paramLabelNumber, paramLabelBoolean, ParamsLabelRegister} from '../BaseSetup';
import {MapboxPlaneSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/MapboxPlane';

export const mapboxPlaneSopParamsLabelRegister: ParamsLabelRegister<typeof MapboxPlaneSopNode, MapboxPlaneSopNode> = {
	nodeClass: MapboxPlaneSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [
				paramLabelNumber(node.p.type),
				paramLabelNumber(node.p.resolution),
				paramLabelNumber(node.p.sizeMult),
				paramLabelBoolean(node.p.fullView),
				paramLabelBoolean(node.p.asPoints),
			];
		},
	}),
};
