import {BaseBuilderMatNodeType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/_BaseBuilder';
import {MatOnCreateHookRegister} from './_Base';
import {MatType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Mat';

export function builderMatOnCreate(node: BaseBuilderMatNodeType) {
	const globals = node.createNode('globals');
	const output = node.createNode('output');

	globals.uiData.setPosition(-200, 0);
	output.uiData.setPosition(200, 0);

	return {globals, output};
}

const MAT_TYPES: MatType[] = [
	MatType.LINE_BASIC_BUILDER,
	MatType.MESH_BASIC_BUILDER,
	MatType.MESH_LAMBERT_BUILDER,
	MatType.MESH_PHONG_BUILDER,
	MatType.MESH_STANDARD_BUILDER,
	MatType.MESH_PHYSICAL_BUILDER,
	MatType.MESH_TOON_BUILDER,
	MatType.MESH_DEPTH_BUILDER,
	MatType.MESH_DISTANCE_BUILDER,
	MatType.POINTS_BUILDER,
	MatType.VOLUME_BUILDER,
];

export const BuilderMatOnCreateHookRegisters = MAT_TYPES.map((matType) => {
	class BuilderOnCreateRegister extends MatOnCreateHookRegister<MatType> {
		override type(): MatType {
			return matType;
		}
		override onCreate(node: any) {
			builderMatOnCreate(node);
		}
	}
	return BuilderOnCreateRegister;
});
