import {TypeAssert} from './../../../../../../@polygonjs/polygonjs/src/engine/poly/Assert';
import {JsConnectionPointType} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';
import {ATTRIBUTE_TYPES, AttribType} from './../../../../../../@polygonjs/polygonjs/src/core/geometry/Constant';
import {CssType} from './../../../ColorUtils';
import {
	paramLabelString,
	paramLabelNumber,
	paramLabelVector2,
	paramLabelVector3,
	paramLabelVector4,
} from './../BaseSetup';
import {AttribCreateSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/AttribCreate';
import {ParamsLabelRegister} from '../BaseSetup';
import {ParamsLabelResult} from '../utils/_Base';
import {typeClassName} from '../../../../core/ColorUtils';

const JS_TYPE: JsConnectionPointType[] = [
	JsConnectionPointType.FLOAT,
	JsConnectionPointType.VECTOR2,
	JsConnectionPointType.VECTOR3,
	JsConnectionPointType.INT, // not.VECTOR4 as it is white
];
function actorType(attribType: AttribType, attribSize: number) {
	switch (attribType) {
		case AttribType.NUMERIC: {
			return JS_TYPE[attribSize - 1];
		}
		case AttribType.STRING: {
			return JsConnectionPointType.COLOR; // not .STRING as it is white
		}
	}
	TypeAssert.unreachable(attribType);
}
const COLOR_STRENGTH = 500;
const DEFAULT_CLASS = 'text-blue-500';
export const attribCreateSopParamsLabelRegister: ParamsLabelRegister<typeof AttribCreateSopNode, AttribCreateSopNode> =
	{
		nodeClass: AttribCreateSopNode,
		setupFunc: (node) => ({
			params: node.params.all,
			callback: () => {
				const attribType = ATTRIBUTE_TYPES[node.pv.type];
				const attribSize = node.pv.size;
				const attribTypeClassName =
					typeClassName(actorType(attribType, attribSize), CssType.TEXT, COLOR_STRENGTH) || DEFAULT_CLASS;

				const list: ParamsLabelResult[] = [];

				if (node.pv.group.trim() != '') {
					list.push(paramLabelString(node.p.group));
				}

				list.push(node.pv.name, paramLabelNumber(node.p.class), paramLabelNumber(node.p.type));

				if (attribType == AttribType.STRING) {
					list.push(node.p.string.rawInput());
				} else {
					list.push(paramLabelNumber(node.p.size, {className: attribTypeClassName}));
					switch (attribSize) {
						case 1: {
							list.push(paramLabelNumber(node.p.value1, {displayLabel: false}));
							break;
						}
						case 2: {
							list.push(paramLabelVector2(node.p.value2, {displayLabel: false}));
							break;
						}
						case 3: {
							list.push(paramLabelVector3(node.p.value3, {displayLabel: false}));
							break;
						}
						case 4: {
							list.push(paramLabelVector4(node.p.value4, {displayLabel: false}));
							break;
						}
					}
				}

				return list;
			},
		}),
	};
