import {MeshBasicMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshBasic';
import {
	colorToParamLabelString,
	ParamsLabelRegister,
	ParamsLabelResult,
	paramLabelBoolean,
	paramLabelNodePathIf,
} from '../BaseSetup';
import {_matSide} from './utils/Sided';

export const meshBasicMatParamsLabelRegister: ParamsLabelRegister<typeof MeshBasicMatNode, MeshBasicMatNode> = {
	nodeClass: MeshBasicMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];

			list.push(colorToParamLabelString(node.pv.color));
			list.push(paramLabelBoolean(node.p.useVertexColors));

			paramLabelNodePathIf(node.p.useMap, node.p.map, list);

			list.push(_matSide(node));

			return list;
		},
	}),
};
