import {paramLabelNumber, paramLabelBoolean, ParamsLabelRegister} from '../BaseSetup';
import {TetrahedronSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Tetrahedron';

export const tetrahedronSopParamsLabelRegister: ParamsLabelRegister<typeof TetrahedronSopNode, TetrahedronSopNode> = {
	nodeClass: TetrahedronSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [
				paramLabelNumber(node.p.radius),
				paramLabelNumber(node.p.detail),
				paramLabelBoolean(node.p.pointsOnly),
			];
		},
	}),
};
