import {TypeAssert} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/Assert';
import {
	BACKGROUND_MODES,
	BackgroundMode,
} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/manager/utils/Scene/Background';
import {SceneObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/Scene';
import {booleanAndIcon, ParamsLabelResult, colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const sceneObjParamsLabelRegister: ParamsLabelRegister<typeof SceneObjNode, SceneObjNode> = {
	nodeClass: SceneObjNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];

			// auto update
			list.push(`${booleanAndIcon(node.pv.autoUpdate)} autoUpdate`);

			// bg
			const backgroundMode = BACKGROUND_MODES[node.pv.backgroundMode];
			list.push(`backgroundMode: ${backgroundMode}`);
			function addBackgroundMode(backgroundMode: BackgroundMode) {
				switch (backgroundMode) {
					case BackgroundMode.NONE: {
						// return this._setBackgroundNone();
						return;
					}
					case BackgroundMode.COLOR: {
						list.push(colorToParamLabelString(node.pv.bgColor));
						return;
					}
					case BackgroundMode.TEXTURE: {
						list.push(node.pv.bgTexture.path());
						return;
					}
				}
				TypeAssert.unreachable(backgroundMode);
			}
			addBackgroundMode(backgroundMode);
			// fog
			list.push(`${booleanAndIcon(node.pv.useFog)} fog`);

			return list;
		},
	}),
};
