import {CADMirrorSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CADMirror';
import {ParamsLabelRegister, paramLabelVector3} from '../BaseSetup';

export const cadMirrorSopParamsLabelRegister: ParamsLabelRegister<typeof CADMirrorSopNode, CADMirrorSopNode> = {
	nodeClass: CADMirrorSopNode,
	setupFunc: (node) => {
		const params = [node.p.axis];
		return {
			params,
			callback: () => {
				return [paramLabelVector3(node.p.axis)];
			},
		};
	},
};
