import {MeshPhysicalMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshPhysical';
import {booleanAndIcon, colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const meshPhysicalMatParamsLabelRegister: ParamsLabelRegister<typeof MeshPhysicalMatNode, MeshPhysicalMatNode> =
	{
		nodeClass: MeshPhysicalMatNode,
		setupFunc: (node) => ({
			params: node.params.all,
			callback: () => {
				const list = [
					colorToParamLabelString(node.pv.color),
					`${booleanAndIcon(node.pv.useVertexColors)} useVertexColors`,
					`${booleanAndIcon(node.pv.useEnvMap)} useEnvMap` +
						(node.pv.useEnvMap ? `: ${node.pv.envMap.path()}` : ``),
					`roughness: ${node.pv.roughness}`,
					`metalness: ${node.pv.metalness}`,
					`transmission: ${node.pv.transmission}`,
				];
				if (node.pv.transmission > 0) {
					list.push(...[`ior: ${node.pv.ior}`, `thickness: ${node.pv.thickness}`]);
				}

				return list;
			},
		}),
	};
