import {ParamsLabelRegister} from '../BaseSetup';
import {SDFFromObjectCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/SDFFromObject';

export const sdfFromObjectCopParamsLabelRegister: ParamsLabelRegister<
	typeof SDFFromObjectCopNode,
	SDFFromObjectCopNode
> = {
	nodeClass: SDFFromObjectCopNode,
	setupFunc: (node) => {
		return {
			params: [node.p.geometry, node.p.voxelSize],
			callback: () => [node.p.geometry.path(), `voxelSize: ${node.pv.voxelSize}`],
		};
	},
};
