import type {AreaLightObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/AreaLight';
import {ObjOnCreateHookRegister} from './_Base';
import {LightType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Light';

function onCreateHook(node: AreaLightObjNode) {
	node.p.showHelper.set(true);
}

export class AreaLightObjOnCreateRegister extends ObjOnCreateHookRegister<LightType.AREA> {
	override type(): LightType.AREA {
		return LightType.AREA;
	}
	override onCreate(node: AreaLightObjNode) {
		onCreateHook(node);
	}
}
