import {ParamsLabelRegister} from '../BaseSetup';
import {ScrollTriggerEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/ScrollTrigger';

export const scrollTriggerEventParamsLabelRegister: ParamsLabelRegister<
	typeof ScrollTriggerEventNode,
	ScrollTriggerEventNode
> = {
	nodeClass: ScrollTriggerEventNode,
	setupFunc: (node) => {
		const params = [
			node.p.active,
			node.p.element,
			node.p.onToggle,
			node.p.onEnter,
			node.p.onLeave,
			node.p.onEnterBack,
			node.p.onLeaveBack,
		];
		return {
			params,
			callback: () => {
				if (node.pv.active) {
					const eventNames = params
						.map((p) => {
							return p.value ? p.name() : undefined;
						})
						.filter((v) => v)
						.join(', ');
					return `${eventNames} (${node.pv.element})`;
				} else {
					return 'x';
				}
			},
		};
	},
};
