import type {PointLightObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/PointLight';
import {ObjOnCreateHookRegister} from './_Base';
import {LightType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Light';

function onCreateHook(node: PointLightObjNode) {
	node.p.showHelper.set(true);
}

export class PointLightObjOnCreateRegister extends ObjOnCreateHookRegister<LightType.POINT> {
	override type(): LightType.POINT {
		return LightType.POINT;
	}
	override onCreate(node: PointLightObjNode) {
		onCreateHook(node);
	}
}
