import { CopOnCreateHookRegister } from './_Base';
import { CopType } from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Cop';

function onCreateHook(node: Builder2DArrayCopNode) {
	const globals = node.createNode('globals');
	const output = node.createNode('output');

	globals.uiData.setPosition(-200, 0);
	output.uiData.setPosition(200, 0);
}

