export class Builder2DArrayCopOnCreateHookRegister extends CopOnCreateHookRegister<CopType.BUILDER_2D_ARRAY> {
	override type(): CopType.BUILDER_2D_ARRAY {
		return CopType.BUILDER_2D_ARRAY
	}
	override onCreate(node: Builder2DArrayCopNode) {
		onCreateHook(node);
	}
}
