import type {CADGroupSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CADGroup';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

function onCreateHook(node: CADGroupSopNode) {
	node.p.name.set(node.name());
}

export class CADGroupSopOnCreateRegister extends SopOnCreateHookRegister<SopType.CAD_GROUP> {
	override type(): SopType.CAD_GROUP {
		return SopType.CAD_GROUP;
	}
	override onCreate(node: CADGroupSopNode) {
		onCreateHook(node);
	}
}
