import {UpdateScenePostNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/post/UpdateScene';
import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';

export const updateScenePostParamsLabelRegister: ParamsLabelRegister<typeof UpdateScenePostNode, UpdateScenePostNode> =
	{
		nodeClass: UpdateScenePostNode,
		setupFunc: (node) => ({
			params: node.params.all,
			callback: () =>
				node.pv.reset
					? [`${booleanAndIcon(node.pv.reset)} reset`]
					: [
							`mask: ${node.pv.objectsMask}`,
							`${booleanAndIcon(node.pv.invertMask)} invertMask`,
							`${booleanAndIcon(node.pv.setMatteMaterial)} setMatteMaterial`,
							`${booleanAndIcon(node.pv.setVisible)} setVisible: ${booleanAndIcon(node.pv.visible)}`,
					  ],
		}),
	};
