import {ParamsLabelResult, ParamsLabelRegister, paramLabelNumber, paramLabelBoolean} from '../BaseSetup';
import {CameraWebXRARSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CameraWebXRAR';
import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';

export const cameraWebXRARSopParamsLabelRegister: ParamsLabelRegister<
	typeof CameraWebXRARSopNode,
	CameraWebXRARSopNode
> = {
	nodeClass: CameraWebXRARSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];

			list.push(paramLabelNumber(node.p.hitTest));
			list.push(paramLabelNumber(node.p.lightEstimation));
			list.push(paramLabelNumber(node.p.cameraAccess));

			list.push(paramLabelBoolean(node.p.overrideReferenceSpaceType));
			if (isBooleanTrue(node.pv.overrideReferenceSpaceType)) {
				list.push(paramLabelNumber(node.p.referenceSpaceType));
			}

			return list;
		},
	}),
};
