import {CADFilletSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CADFillet';
import {paramLabelString, paramLabelNumber, ParamsLabelRegister, ParamsLabelResult} from '../BaseSetup';

export const cadFilletSopParamsLabelRegister: ParamsLabelRegister<typeof CADFilletSopNode, CADFilletSopNode> = {
	nodeClass: CADFilletSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];
			if (node.pv.group.trim() != '') {
				list.push(paramLabelString(node.p.group));
			}
			list.push(...[paramLabelNumber(node.p.mode), paramLabelNumber(node.p.radius)]);

			return list;
		},
	}),
};
