import type {ParticlesSystemGpuSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/ParticlesSystemGpu';
import type {ActorSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Actor';
import type {PointsBuilderMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/PointsBuilder';
import type {MaterialsNetworkSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/MaterialsNetwork';
import type {GeoObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/Geo';
import {JsConnectionPointType} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';
import {CoreString} from './../../../../../../@polygonjs/polygonjs/src/core/String';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {MatType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Mat';

const MAT_NAME = 'MAT';
const PARTICLES_MAT_NAME = 'pointsParticles';
const ACTOR_NODE_BASE_NAME = 'actor_particles';

function _onCreatePrepareMaterial(node: ParticlesSystemGpuSopNode) {
	// that's mostly to have the default shader work when creating the node
	// output.setInput('position', globals, 'position')
	// or instead we could create the default shader

	function createMatNetwork(geoNode: GeoObjNode) {
		const MAT = geoNode.createNode('materialsNetwork');
		MAT.setName(MAT_NAME);

		const nodePos = node.uiData.position();
		MAT.uiData.setPosition(nodePos.x - 200, nodePos.y);

		return MAT;
	}

	const geoNode = node.parent() as GeoObjNode;
	const MAT: MaterialsNetworkSopNode = geoNode.nodesByType('materialsNetwork')[0] || createMatNetwork(geoNode);

	function createMat(MAT: MaterialsNetworkSopNode) {
		let pointsMat = MAT.node('points_builder1') as PointsBuilderMatNode;
		if (!(pointsMat && pointsMat.type() == MatType.POINTS_BUILDER)) {
			pointsMat = MAT.createNode('pointsBuilder');
			const output1 = pointsMat.createNode('output');
			const globals = pointsMat.createNode('globals');
			globals.uiData.setPosition(-200, 0);
			output1.uiData.setPosition(200, 0);
		}
		pointsMat.setName(PARTICLES_MAT_NAME);

		let pointsMatConstantPointSize = pointsMat.node('constant');
		if (!(pointsMatConstantPointSize && pointsMatConstantPointSize.type() == GlType.CONSTANT)) {
			pointsMatConstantPointSize = pointsMat.createNode('constant');
			pointsMatConstantPointSize.setName('constant_point_size');
		}
		pointsMatConstantPointSize.p.float.set(0.04); // to match the default point material
		const points_mat_output1 = pointsMat.node('output1');
		if (points_mat_output1) {
			points_mat_output1.setInput('gl_PointSize', pointsMatConstantPointSize);
		}
		return pointsMat;
	}
	const pointsMat = MAT.node(PARTICLES_MAT_NAME) || createMat(MAT);
	if (pointsMat) {
		node.p.material.setNode(pointsMat, {relative: true});
	}
}
function _onCreatePrepareActorNode(node: ParticlesSystemGpuSopNode) {
	function createActorNodeChildren(actorNode: ActorSopNode) {
		const particlesSystemReset = actorNode.createNode('particlesSystemReset');
		const onScenePause = actorNode.createNode('onScenePause');
		const particlesSystemStepSimulation = actorNode.createNode('particlesSystemStepSimulation');
		const onTick = actorNode.createNode('onTick');

		particlesSystemReset.setInput(0, onScenePause);
		particlesSystemStepSimulation.setInput(JsConnectionPointType.TRIGGER, onTick);

		onScenePause.uiData.setPosition(-100, -100);
		particlesSystemReset.uiData.setPosition(100, -100);
		onTick.uiData.setPosition(-100, 100);
		particlesSystemStepSimulation.uiData.setPosition(100, 100);
	}
	function createActorNode(geoNode: GeoObjNode) {
		const actor1 = geoNode.createNode('actor');
		actor1.setName(`${ACTOR_NODE_BASE_NAME}${CoreString.tailDigits(node.name())}`);

		const nodePos = node.uiData.position();
		actor1.uiData.setPosition(nodePos.x, nodePos.y - 200);

		createActorNodeChildren(actor1);

		return actor1;
	}

	const geoNode = node.parent() as GeoObjNode;
	const actor1: ActorSopNode = /*geoNode.nodesByType('actor')[0] ||*/ createActorNode(geoNode);

	return {actor1};
	// const pointsMat = MAT.node(particlesMatName) || createMat(MAT);
	// if (pointsMat) {
	// 	node.p.material.setNode(pointsMat, {relative: true});
	// }
}

function onCreateHook(node: ParticlesSystemGpuSopNode) {
	// if (this.assembler_controller) {
	// we use the method _on_create here, to link the global to the output on create,
	// as this seems to prevent a bug when the particle system is created
	// without exported textures
	// and the object fails to render until a full page reload.
	// this.lifecycle.add_on_create_hook(this._on_create.bind(this));
	// this.lifecycle.add_on_create_hook(this.assembler_controller.on_create.bind(this.assembler_controller));
	// }
	// this.lifecycle.add_on_create_hook(this._on_create_prepare_material_bound);
	const current_global = node.nodesByType('globals')[0];
	const current_output = node.nodesByType('output')[0];
	if (current_global || current_output) {
		return;
	}
	const globals = node.createNode('globals');
	const output1 = node.createNode('output');
	const noise1 = node.createNode('noise');
	const add1 = node.createNode('add');

	noise1.setInput('position', globals, 'position');
	add1.setInput(0, globals, 'position');
	add1.setInput(1, noise1);
	output1.setInput('position', add1);

	const amp = noise1.params.get('amp');
	if (amp) {
		amp.set([0.1, 0.1, 0.1]);
	}

	globals.uiData.setPosition(-300, 0);
	output1.uiData.setPosition(200, 0);
	noise1.uiData.setPosition(-150, 100);
	add1.uiData.setPosition(0, 0);

	_onCreatePrepareMaterial(node);
	const {actor1} = _onCreatePrepareActorNode(node);
	actor1.setInput(0, node.io.inputs.input(0));
	node.setInput(0, actor1);
}
export class ParticlesSystemGPUSopOnCreateRegister extends SopOnCreateHookRegister<SopType.PARTICLES_SYSTEM_GPU> {
	override type(): SopType.PARTICLES_SYSTEM_GPU {
		return SopType.PARTICLES_SYSTEM_GPU;
	}
	override onCreate(node: ParticlesSystemGpuSopNode) {
		onCreateHook(node);
	}
}
