import {NodePathParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/NodePath';
import {ArrayUtils} from './../../../../../../@polygonjs/polygonjs/src/core/ArrayUtils';
import {
	ERROR_CLASS,
	EXPRESSION_CLASS,
	ParamLabelOptions,
	ParamLabelResultColoredText,
	ParamsLabelResult,
} from './_Base';
import {isBooleanTrue} from './../../../../../../@polygonjs/polygonjs/src/core/Type';
import {BooleanParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Boolean';

export function _paramLabelNodePath(param: NodePathParam, options?: ParamLabelOptions): ParamsLabelResult {
	const label = options?.displayLabel == false ? undefined : `${param.name()}:`;
	const compactElements: Array<string | ParamLabelResultColoredText> = [];
	if (!param.value.node()) {
		ArrayUtils.compact(
			[
				label,
				{
					label: param.rawInput() || 'no node given',
					className: ERROR_CLASS,
					bold: true,
				},
			],
			compactElements
		);
		return {
			detailed: true,
			elements: compactElements,
		};
	}

	if (param.hasExpression()) {
		ArrayUtils.compact(
			[
				label,
				{
					label: param.rawInput(),
					className: EXPRESSION_CLASS,
					bold: true,
				},
			],
			compactElements
		);
		return {
			detailed: true,
			elements: compactElements,
		};
	} else {
		const className = options?.className;
		if (className) {
			ArrayUtils.compact(
				[
					label,
					{
						label: param.rawInput(),
						className,
					},
				],
				compactElements
			);
			return {
				detailed: true,
				elements: compactElements,
			};
		} else {
			ArrayUtils.compact([label, param.rawInput()], compactElements);
			return compactElements.join(' ');
		}
	}
}

export function _paramLabelNodePathIf(
	conditionParam: BooleanParam,
	nodePathParam: NodePathParam,
	list: ParamsLabelResult[]
) {
	if (isBooleanTrue(conditionParam.value)) {
		list.push(_paramLabelNodePath(nodePathParam));
	}
}
