import {ParamsLabelRegister} from '../BaseSetup';
import {Builder2DArrayCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/Builder2DArray';

export const builder2DArrayCopParamsLabelRegister: ParamsLabelRegister<
	typeof Builder2DArrayCopNode,
	Builder2DArrayCopNode
> = {
	nodeClass: Builder2DArrayCopNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: () => [
				`${node.pv.resolution.x} x ${node.pv.resolution.y} x ${node.pv.layers} (= ${
					node.pv.resolution.x * node.pv.resolution.y * node.pv.layers
				})`,
				...node.textureParamsController.paramLabels(),
			],
		};
	},
};
