import {paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';
import {PerspectiveCameraSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PerspectiveCamera';

export const perspectiveCameraSopParamsLabelRegister: ParamsLabelRegister<
	typeof PerspectiveCameraSopNode,
	PerspectiveCameraSopNode
> = {
	nodeClass: PerspectiveCameraSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [paramLabelNumber(node.p.fov), paramLabelNumber(node.p.near), paramLabelNumber(node.p.far)];
		},
	}),
};
