export const fileDRCSopParamsLabelRegister: ParamsLabelRegister<typeof FileDRCSopNode, FileDRCSopNode> = {
	nodeClass: FileDRCSopNode,
	setupFunc: (node) => ({
		params: node.p.url,
		callback: () => paramLabelString(node.p.url),
	}),
};
