export const fileGLTFSopParamsLabelRegister: ParamsLabelRegister<typeof FileGLTFSopNode, FileGLTFSopNode> = {
	nodeClass: FileGLTFSopNode,
	setupFunc: (node) => ({
		params: [node.p.url, node.p.draco],
		callback: () => {
			if (isBooleanTrue(node.pv.draco)) {
				return [paramLabelString(node.p.url), paramLabelBoolean(node.p.draco)];
			} else {
				return paramLabelString(node.p.url);
			}
		},
	}),
};
