import {paramLabelNumber, paramLabelVector3, ParamsLabelRegister} from '../BaseSetup';
import {RoundedBoxSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/RoundedBox';

export const roundedBoxSopParamsLabelRegister: ParamsLabelRegister<typeof RoundedBoxSopNode, RoundedBoxSopNode> = {
	nodeClass: RoundedBoxSopNode,
	setupFunc: (node) => ({
		params: [node.p.sizes, node.p.size, node.p.bevel],
		callback: () => {
			return [paramLabelVector3(node.p.sizes), paramLabelNumber(node.p.size), paramLabelNumber(node.p.bevel)];
		},
	}),
};
