import {MeshToonMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshToon';
import {booleanAndIcon, colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const meshToonMatParamsLabelRegister: ParamsLabelRegister<typeof MeshToonMatNode, MeshToonMatNode> = {
	nodeClass: MeshToonMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			colorToParamLabelString(node.pv.color),
			`${booleanAndIcon(node.pv.useVertexColors)} useVertexColors`,
		],
	}),
};
