import {TypeAssert} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/Assert';
import {
	AnimTargetNodeTargetType,
	TargetAnimNode,
	ANIM_TARGET_TYPES,
} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/anim/Target';
import {ParamsLabelRegister} from '../BaseSetup';

export const targetAnimParamsLabelRegister: ParamsLabelRegister<typeof TargetAnimNode, TargetAnimNode> = {
	nodeClass: TargetAnimNode,
	setupFunc: (node) => ({
		params: [node.p.type, node.p.nodePath, node.p.objectMask],
		callback: () => {
			const type = ANIM_TARGET_TYPES[node.pv.type];
			switch (type) {
				case AnimTargetNodeTargetType.NODE:
					return node.pv.nodePath.path();
				case AnimTargetNodeTargetType.SCENE_GRAPH:
					return node.pv.objectMask;
			}
			TypeAssert.unreachable(type);
		},
	}),
};
