import type {VaryingReadGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/VaryingRead';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {GlOnCreateHookRegister} from './_Base';

export function onCreateHook(node: VaryingReadGlNode) {
	node.p.name.set(node.name());
}

export class VaryingReadGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.VARYING_READ> {
	override type(): GlType.VARYING_READ {
		return GlType.VARYING_READ;
	}
	override onCreate(node: VaryingReadGlNode) {
		onCreateHook(node);
	}
}
