import type {ParamJsNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/Param';
import {JsType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Js';
import {JsOnCreateHookRegister} from './_Base';

function onCreateHook(node: ParamJsNode) {
	node.p.name.set(node.name());
}

export class ParamJsOnCreateHookRegister extends JsOnCreateHookRegister<JsType.PARAM> {
	override type(): JsType.PARAM {
		return JsType.PARAM;
	}
	override onCreate(node: ParamJsNode) {
		onCreateHook(node);
	}
}
