import {ParamsLabelResult} from './../BaseSetup';
import {CopySopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Copy';
import {booleanAndIcon, paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';

export const copySopParamsLabelRegister: ParamsLabelRegister<typeof CopySopNode, CopySopNode> = {
	nodeClass: CopySopNode,
	setupFunc: (node) => ({
		params: [
			node.p.count,
			node.p.transformOnly,
			node.p.transformMode,
			node.p.copyAttributes,
			node.p.attributesToCopy,
			node.p.useCopyExpr,
		],
		callback: () => {
			const list: ParamsLabelResult[] = [];

			list.push(paramLabelNumber(node.p.count));
			list.push(paramLabelNumber(node.p.transformMode));
			list.push(
				`${booleanAndIcon(node.pv.copyAttributes)} ${
					node.pv.copyAttributes ? `copyAttributes:'${node.pv.attributesToCopy}'` : 'no attributes copied'
				}`
			);
			list.push(`${booleanAndIcon(node.pv.useCopyExpr)} useCopyExpr`);

			return list;
		},
	}),
};
