import {MeshPhongMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshPhong';
import {booleanAndIcon, colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const meshPhongMatParamsLabelRegister: ParamsLabelRegister<typeof MeshPhongMatNode, MeshPhongMatNode> = {
	nodeClass: MeshPhongMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			colorToParamLabelString(node.pv.color),
			`${booleanAndIcon(node.pv.useVertexColors)} useVertexColors`,
			`${booleanAndIcon(node.pv.useEnvMap)} useEnvMap` + (node.pv.useEnvMap ? `: ${node.pv.envMap.path()}` : ``),
		],
	}),
};
