import {ParamsLabelRegister, paramLabelBoolean} from '../BaseSetup';
import {LightMapCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/LightMap';

export const lightMapCopParamsLabelRegister: ParamsLabelRegister<typeof LightMapCopNode, LightMapCopNode> = {
	nodeClass: LightMapCopNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: () => [paramLabelBoolean(node.p.blur), node.pv.objectsMask, node.pv.lightsMask],
		};
	},
};
