import {BaseMatNodeType} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/_Base';
import type {MaterialSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Material';
import type {MaterialsNetworkSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/MaterialsNetwork';
import {GeoObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/Geo';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

const MAT_NAME = 'MAT';
const MESH_STANDARD_MAT_NAME = 'meshStandard1';

function onCreateHook(node: MaterialSopNode) {
	const geoNode = node.parent() as GeoObjNode;

	function createMatNetwork(geoNode: GeoObjNode) {
		const MAT = geoNode.createNode('materialsNetwork');
		MAT.setName(MAT_NAME);

		const nodePos = node.uiData.position();
		MAT.uiData.setPosition(nodePos.x - 200, nodePos.y);

		return MAT;
	}

	const MAT = geoNode.nodesByType('materialsNetwork')[0] || createMatNetwork(geoNode);

	function createMeshMaterial(MAT: MaterialsNetworkSopNode) {
		// ensure we don't link to a CopNetwork for instance
		const existingMatNodes = MAT.children().filter((node: BaseMatNodeType) => node.material != null);
		const materialNode = existingMatNodes[0] || MAT.createNode('meshStandard');

		return materialNode;
	}
	const matNode = MAT.node(MESH_STANDARD_MAT_NAME) || createMeshMaterial(MAT);

	node.p.material.setNode(matNode, {relative: true});
}

export class MaterialSopOnCreateRegister extends SopOnCreateHookRegister<SopType.MATERIAL> {
	override type(): SopType.MATERIAL {
		return SopType.MATERIAL;
	}
	override onCreate(node: MaterialSopNode) {
		onCreateHook(node);
	}
}
