import {ColorCorrectGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/ColorCorrect';
import {ParamsLabelRegister} from '../BaseSetup';

export const colorCorrectGlParamsLabelRegister: ParamsLabelRegister<typeof ColorCorrectGlNode, ColorCorrectGlNode> = {
	nodeClass: ColorCorrectGlNode,
	setupFunc: (node) => ({
		params: [node.p.from, node.p.to],
		callback: () => {
			const spaces = node.colorSpaces();
			return `${spaces.from} -> ${spaces.to}`;
		},
	}),
};
