import {MapboxSatelliteCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/MapboxSatellite';
import {ParamsLabelRegister, paramLabelNumber, paramLabelBoolean} from '../BaseSetup';

export const mapboxSatelliteCopParamsLabelRegister: ParamsLabelRegister<
	typeof MapboxSatelliteCopNode,
	MapboxSatelliteCopNode
> = {
	nodeClass: MapboxSatelliteCopNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: () => {
				return [
					paramLabelNumber(node.p.longitude),
					paramLabelNumber(node.p.latitude),
					paramLabelNumber(node.p.zoom),
					paramLabelBoolean(node.p.highres),
				];
			},
		};
	},
};
