import {
	MergeAnimNode,
	EASING_ANIM_MERGE_MODES,
} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/anim/Merge';
import {ParamsLabelRegister} from '../BaseSetup';

export const mergeAnimParamsLabelRegister: ParamsLabelRegister<typeof MergeAnimNode, MergeAnimNode> = {
	nodeClass: MergeAnimNode,
	setupFunc: (node) => ({
		params: [node.p.mode, node.p.offset],
		callback: () => {
			const mode = EASING_ANIM_MERGE_MODES[node.pv.mode];
			const offset = node.pv.offset;
			return `${mode} (offset: ${offset})`;
		},
	}),
};
