import type {ParamGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/Param';
import { GlType } from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import { GlOnCreateHookRegister } from './_Base';

export function onCreateHook(node: ParamGlNode) {
	node.p.name.set(node.name());
}



export class ParamGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.PARAM> {
	override type(): GlType.PARAM {
		return GlType.PARAM
	}
	override onCreate(node: ParamGlNode) {
		onCreateHook(node);
	}
}