import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';
import {PlaneSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Plane';

export const planeSopParamsLabelRegister: ParamsLabelRegister<typeof PlaneSopNode, PlaneSopNode> = {
	nodeClass: PlaneSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list = [
				`direction: ${node.pv.direction.toArray()}`,
				`size: ${node.pv.size.toArray()}`,
				`${booleanAndIcon(node.pv.useSegmentsCount)} useSegmentsCount`,
			];

			if (node.pv.useSegmentsCount) {
				list.push(...[`segments: ${node.pv.segments.toArray()}`]);
			} else {
				list.push(...[`stepSize: ${node.pv.stepSize}`]);
			}

			list.push(`${booleanAndIcon(node.pv.asLines)} asLines`);

			return list;
		},
	}),
};
