import {AttribType} from '../../../../../../@polygonjs/polygonjs/src/core/geometry/Constant';
import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {DeleteSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Delete';
import {
	paramLabelBoolean,
	ParamsLabelRegister,
	ParamsLabelResult,
	paramLabelNumber,
	paramLabelString,
	paramLabelVector2,
	paramLabelVector3,
	paramLabelVector4,
} from '../BaseSetup';

export const deleteSopParamsLabelRegister: ParamsLabelRegister<typeof DeleteSopNode, DeleteSopNode> = {
	nodeClass: DeleteSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [paramLabelNumber(node.p.class), paramLabelBoolean(node.p.invert)];

			//

			if (isBooleanTrue(node.pv.byObjectType)) {
				list.push(paramLabelBoolean(node.p.byObjectType));
				list.push(paramLabelNumber(node.p.objectType));
			}
			//
			if (isBooleanTrue(node.pv.byExpression)) {
				list.push(paramLabelBoolean(node.p.byExpression));
				list.push(paramLabelBoolean(node.p.expression));
			}

			//
			if (isBooleanTrue(node.pv.byAttrib)) {
				list.push(paramLabelBoolean(node.p.byAttrib));
				list.push(paramLabelNumber(node.p.attribType));
				list.push(paramLabelString(node.p.attribName));
				switch (node.attribType()) {
					case AttribType.STRING: {
						list.push(paramLabelString(node.p.attribString));
						break;
					}
					case AttribType.NUMERIC: {
						list.push(paramLabelNumber(node.p.attribSize));
						switch (node.pv.attribSize) {
							case 1: {
								list.push(paramLabelNumber(node.p.attribValue1));
								break;
							}
							case 2: {
								list.push(paramLabelVector2(node.p.attribValue2));
								break;
							}
							case 3: {
								list.push(paramLabelVector3(node.p.attribValue3));
								break;
							}
							case 4: {
								list.push(paramLabelVector4(node.p.attribValue4));
								break;
							}
						}
						break;
					}
				}
			}

			//
			if (isBooleanTrue(node.pv.byBbox)) {
				list.push(paramLabelBoolean(node.p.byBbox));
				list.push(paramLabelVector3(node.p.bboxSize));
				list.push(paramLabelVector3(node.p.bboxCenter));
			}
			//
			if (isBooleanTrue(node.pv.byBoundingObject)) {
				list.push(paramLabelBoolean(node.p.byBoundingObject));
			}

			list.push(paramLabelBoolean(node.p.keepPoints));
			return list;
		},
	}),
};
