import type {ForLoopJsNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/ForLoop';
import {JsType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Js';
import {JsOnCreateHookRegister} from './_Base';

export function onCreateHook(node: ForLoopJsNode) {
	node.p.inputsCount.set(1);
	const subnetInput1 = node.createNode('subnetInput');
	const subnetOutput1 = node.createNode('subnetOutput');

	subnetOutput1.setInput(0, subnetInput1, 4);

	subnetInput1.uiData.setPosition(-100, 0);
	subnetOutput1.uiData.setPosition(+100, 0);
}

export class ForLoopJsOnCreateHookRegister extends JsOnCreateHookRegister<JsType.FOR_LOOP> {
	override type(): JsType.FOR_LOOP {
		return JsType.FOR_LOOP;
	}
	override onCreate(node: ForLoopJsNode) {
		onCreateHook(node);
	}
}
