import {StringParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/String';
import {ArrayUtils} from './../../../../../../@polygonjs/polygonjs/src/core/ArrayUtils';
import {
	COLOR_STRENGTH,
	DEFAULT_CLASS,
	EXPRESSION_CLASS,
	MENU_ENTRIES_JS_TYPE,
	ParamLabelOptions,
	ParamLabelResultColoredText,
	ParamsLabelResult,
} from './_Base';
import {
	NumericParamOptionsMenuEntry,
	StringParamOptionsMenuEntry,
} from '../../../../../../@polygonjs/polygonjs/src/engine/params/utils/OptionsController';
import {CssType, typeClassName} from './../../../ColorUtils';

const MAX_LENGTH = 24;
function truncate(word: string): string {
	if (word.length < MAX_LENGTH) {
		return word;
	} else {
		const elements = word.split('/');
		const reversedElements = elements.reverse();
		let newWord: string = reversedElements.shift()!;
		for (let element of reversedElements) {
			newWord = element + '/' + newWord;
			if (newWord.length > MAX_LENGTH) {
				return `.../${newWord}`;
			}
		}
		return newWord;
	}
}

export function _paramLabelString(param: StringParam, options?: ParamLabelOptions): ParamsLabelResult {
	const label = options?.displayLabel == false ? undefined : `${param.name()}:`;
	const compactElements: Array<string | ParamLabelResultColoredText> = [];
	if (param.hasExpression()) {
		ArrayUtils.compact(
			[
				label,
				{
					label: truncate(param.rawInput()),
					className: EXPRESSION_CLASS,
					bold: true,
				},
				// not sure that the result should be displayed, as it would not make sense when using a copy node for instance.
				// nor when using params which are per entity, but that could be detected.
				// maybe it is possible to detect when using a copy?
				// '(=',
				// {
				// 	label: param.value,
				// 	className: EXPRESSION_RESULT,
				// },
				// ')',
			],
			compactElements
		);
		return {
			detailed: true,
			elements: compactElements,
		};
		// return `${param.name()}: ${param.rawInput()} (=${param.value})`;
	} else {
		if (param.options.hasMenu()) {
			const availableEntries = param.options.menuEntries();
			const selectedEntry =
				availableEntries.find(
					(entry: NumericParamOptionsMenuEntry | StringParamOptionsMenuEntry) => entry.value == param.value
				) || availableEntries[0];
			ArrayUtils.compact(
				[
					label,
					{
						label: selectedEntry.name,
						className:
							options?.className ||
							typeClassName(MENU_ENTRIES_JS_TYPE[0 /*param.value*/], CssType.TEXT, COLOR_STRENGTH) ||
							DEFAULT_CLASS,
					},
				],
				compactElements
			);
			return {
				detailed: true,
				elements: compactElements,
			};
		} else {
			const className = options?.className;
			const compactElements: Array<string | ParamLabelResultColoredText> = [];
			if (className) {
				ArrayUtils.compact(
					[
						label,
						{
							label: truncate(param.rawInput()),
							className,
						},
					],
					compactElements
				);
				return {
					detailed: true,
					elements: compactElements,
				};
			} else {
				ArrayUtils.compact([label, truncate(param.rawInput())], compactElements);
				return compactElements.join(' ');
			}
		}
	}
}
