import {ParamsLabelRegister} from '../BaseSetup';
import {LutCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/Lut';

function getCallback(node: LutCopNode) {
	const url = node.p.url.rawInput();
	if (url) {
		const elements = url.split('/');
		const urlLabel = elements[elements.length - 1];
		return urlLabel;
	} else {
		return '';
	}
}

export const lutCopParamsLabelRegister: ParamsLabelRegister<typeof LutCopNode, LutCopNode> = {
	nodeClass: LutCopNode,
	setupFunc: (node) => {
		return {
			params: [node.p.url],
			callback: () => getCallback(node),
		};
	},
};
