import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {GetObjectJsNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/GetObject';
import {ParamsLabelRegister} from '../BaseSetup';

export const getObjectJsNodeJsParamsLabelRegister: ParamsLabelRegister<typeof GetObjectJsNode, GetObjectJsNode> = {
	nodeClass: GetObjectJsNode,
	setupFunc: (node) => ({
		params: [node.p.getCurrentObject, node.p.mask],
		callback: () => {
			if (isBooleanTrue(node.pv.getCurrentObject)) {
				return 'currentObject';
			} else {
				return node.p.mask.rawInput();
			}
		},
	}),
};
