import { NetworkNodeType } from '../../../../../../@polygonjs/polygonjs/src/engine/poly/NodeContext';
import type {DecomposeSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Decompose';
import {SopOnCreateHookRegister} from './_Base';

function onCreateHook(node: DecomposeSopNode) {
	const subnetInput1 = node.createNode('subnetInput');
	const subnetOutput1 = node.createNode('subnetOutput');

	subnetOutput1.setInput(0, subnetInput1);

	subnetInput1.uiData.setPosition(0, -100);
	subnetOutput1.uiData.setPosition(0, +100);
}

export class DecomposeSopOnCreateRegister extends SopOnCreateHookRegister<NetworkNodeType.DECOMPOSE> {
	override type(): NetworkNodeType.DECOMPOSE{
		return NetworkNodeType.DECOMPOSE
	}
	override onCreate(node: DecomposeSopNode) {
		onCreateHook(node);
	}
}