import {ColorMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/Color';
import {booleanAndIcon, colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const colorMatParamsLabelRegister: ParamsLabelRegister<typeof ColorMatNode, ColorMatNode> = {
	nodeClass: ColorMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			colorToParamLabelString(node.pv.color),
			`${booleanAndIcon(node.pv.useVertexColors)} useVertexColors`,
		],
	}),
};
