import {ColorCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/Color';
import {ParamsLabelRegister, paramLabelColor, paramLabelVector2, paramLabelNumber} from '../BaseSetup';

export const colorCopParamsLabelRegister: ParamsLabelRegister<typeof ColorCopNode, ColorCopNode> = {
	nodeClass: ColorCopNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: () => {
				return [
					paramLabelVector2(node.p.resolution),
					paramLabelColor(node.p.color),
					paramLabelNumber(node.p.alpha),
				];
			},
		};
	},
};
