import {BooleanParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Boolean';
import {ArrayUtils} from './../../../../../../@polygonjs/polygonjs/src/core/ArrayUtils';
import {CoreType} from './../../../../../../@polygonjs/polygonjs/src/core/Type';
import {EXPRESSION_CLASS, ParamLabelOptions, ParamsLabelResult, TRUE, FALSE, ParamLabelResultColoredText} from './_Base';

export function _booleanAndIcon(value: boolean) {
	return value ? TRUE : FALSE;
	// return `${value ? '' : ''} (${value})`;
}
export function _paramLabelBoolean(param: BooleanParam, options?: ParamLabelOptions): ParamsLabelResult {
	const label = options?.displayLabel == false ? undefined : `${param.name()}:`;
	const rawInput = param.rawInput();
	const compactElements:Array<string | ParamLabelResultColoredText> = []
	const rawInputLabel = CoreType.isBoolean(rawInput)
		? _booleanAndIcon(rawInput)
		: rawInput == 1
		? _booleanAndIcon(rawInput == 1)
		: rawInput;
	if (param.hasExpression()) {
		ArrayUtils.compact([
			label,
			{
				label: rawInputLabel,
				className: EXPRESSION_CLASS,
				bold: true,
			},
			// not sure that the result should be displayed, as it would not make sense when using a copy node for instance.
			// nor when using params which are per entity, but that could be detected.
			// maybe it is possible to detect when using a copy?
			// '(=',
			// {
			// 	label: param.value,
			// 	className: EXPRESSION_RESULT,
			// },
			// ')',
		],compactElements)
		return {
			detailed: true,
			elements: compactElements,
		};
		// return `${param.name()}: ${param.rawInput()} (=${param.value})`;
	} else {
		// if (param.options.hasMenu()) {
		// 	const values = param.options.menuEntries();
		// 	const selectedEntry = values[param.value];
		// 	return {
		// 		detailed: true,
		// 		elements: ArrayUtils.compact([
		// 			label,
		// 			{
		// 				label: selectedEntry.name,
		// 				className:
		// 					options?.className ||
		// 					typeClassName(MENU_ENTRIES_ACTOR_TYPE[param.value], CssType.TEXT, COLOR_STRENGTH) ||
		// 					DEFAULT_CLASS,
		// 			},
		// 		]),
		// 	};
		// } else {
		const className = options?.className;
		if (className) {
			ArrayUtils.compact([
				_booleanAndIcon(param.value),
				{
					label: rawInputLabel,
					className,
				},
			],compactElements)
			return {
				detailed: true,
				elements: compactElements,
			};
		} else {
			ArrayUtils.compact([label, _booleanAndIcon(param.value)],compactElements)
			return compactElements.join(' ');
		}
		// }
	}
}
