import {SpotLightObjNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/SpotLight';
import {SpotLightSopNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/SpotLight';
import {PointLightSopNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PointLight';
import {PointLightObjNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/PointLight';
import {DirectionalLightObjNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/DirectionalLight';
import {DirectionalLightSopNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/DirectionalLight';
import {HemisphereLightObjNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/HemisphereLight';
import {HemisphereLightSopNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/HemisphereLight';
import {AmbientLightObjNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/AmbientLight';
import {AmbientLightSopNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/AmbientLight';
import {colorToParamLabelString} from '../BaseSetup';
import {AreaLightObjNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/AreaLight';
import {AreaLightSopNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/AreaLight';
interface helperOwner {
	pv: {
		showHelper: boolean;
	};
}
function lightShowHelper(node: helperOwner): string {
	return node.pv.showHelper ? '+ helper visible' : ' light only';
}
interface intensityOwner {
	pv: {
		intensity: number;
	};
}
function intensity(node: intensityOwner): string {
	return `intensity: ${node.pv.intensity}`;
}

export function paramsLabelAmbientLight(node: AmbientLightSopNode | AmbientLightObjNode) {
	return [colorToParamLabelString(node.pv.color), intensity(node)];
}
export function paramsLabelAreaLight(node: AreaLightSopNode | AreaLightObjNode) {
	return [
		colorToParamLabelString(node.pv.color),
		intensity(node),
		`size: ${node.pv.width}x${node.pv.height}`,
		lightShowHelper(node),
	];
}
export function paramsLabelDirectionalLight(node: DirectionalLightSopNode | DirectionalLightObjNode) {
	return [colorToParamLabelString(node.pv.color), intensity(node), lightShowHelper(node)];
}
export function paramsLabelHemisphereLight(node: HemisphereLightSopNode | HemisphereLightObjNode) {
	return [colorToParamLabelString(node.pv.skyColor), colorToParamLabelString(node.pv.groundColor), intensity(node)];
}
export function paramsLabelPointLight(node: PointLightSopNode | PointLightObjNode) {
	return [colorToParamLabelString(node.pv.color), intensity(node), lightShowHelper(node)];
}
export function paramsLabelSpotLight(node: SpotLightSopNode | SpotLightObjNode) {
	return [colorToParamLabelString(node.pv.color), intensity(node), lightShowHelper(node)];
}
