import {ColorParam} from '../../../../../../@polygonjs/polygonjs/src/engine/params/Color';
import {ConstantGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/Constant';
import {colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const constantGlParamsLabelRegister: ParamsLabelRegister<typeof ConstantGlNode, ConstantGlNode> = {
	nodeClass: ConstantGlNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const param = node.currentParam();
			if (param instanceof ColorParam) {
				return colorToParamLabelString(param.value);
			} else {
				return node.currentValue() || '';
			}
		},
	}),
};
