import {JsConnectionPointType} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';
import type {PhysicsWorldSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PhysicsWorld';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

function onCreateHook(node: PhysicsWorldSopNode) {
	const physicsWorldReset = node.createNode('physicsWorldReset');
	const onScenePause = node.createNode('onScenePause');
	const physicsWorldStepSimulation = node.createNode('physicsWorldStepSimulation');
	const onTick = node.createNode('onTick');

	physicsWorldReset.setInput(0, onScenePause);
	physicsWorldStepSimulation.setInput(JsConnectionPointType.TRIGGER, onTick);

	onScenePause.uiData.setPosition(-100, -100);
	physicsWorldReset.uiData.setPosition(100, -100);
	onTick.uiData.setPosition(-100, 100);
	physicsWorldStepSimulation.uiData.setPosition(100, 100);
}

export class PhysicsWorldSopOnCreateRegister extends SopOnCreateHookRegister<SopType.PHYSICS_WORLD> {
	override type(): SopType.PHYSICS_WORLD {
		return SopType.PHYSICS_WORLD;
	}
	override onCreate(node: PhysicsWorldSopNode) {
		onCreateHook(node);
	}
}
