import type {SDFBuilderSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/SDFBuilder';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

function onCreateHook(node: SDFBuilderSopNode) {
	const globals = node.createNode('globals');
	const output1 = node.createNode('output');
	const SDFSphere1 = node.createNode('SDFSphere');

	SDFSphere1.setInput(0, globals, 'position');
	output1.setInput('d', SDFSphere1);

	SDFSphere1.p.radius.set(0.4);

	globals.uiData.setPosition(-400, 0);
	output1.uiData.setPosition(0, 0);
	SDFSphere1.uiData.setPosition(-200, 0);
}

export class SDFBuilderSopOnCreateRegister extends SopOnCreateHookRegister<SopType.SDF_BUILDER> {
	override type(): SopType.SDF_BUILDER {
		return SopType.SDF_BUILDER;
	}
	override onCreate(node: SDFBuilderSopNode) {
		onCreateHook(node);
	}
}
