import type {IfThenGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/IfThen';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {GlOnCreateHookRegister} from './_Base';

function onCreateHook(node: IfThenGlNode) {
	node.p.inputsCount.set(1);
	const subnetInput1 = node.createNode('subnetInput');
	const subnetOutput1 = node.createNode('subnetOutput');

	subnetOutput1.setInput(0, subnetInput1, 0);

	subnetInput1.uiData.setPosition(-100, 0);
	subnetOutput1.uiData.setPosition(+100, 0);
}

export class IfThenGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.IF_THEN> {
	override type(): GlType.IF_THEN {
		return GlType.IF_THEN;
	}
	override onCreate(node: IfThenGlNode) {
		onCreateHook(node);
	}
}
