import {paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';
import {SphereSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Sphere';

export const sphereSopParamsLabelRegister: ParamsLabelRegister<typeof SphereSopNode, SphereSopNode> = {
	nodeClass: SphereSopNode,
	setupFunc: (node) => ({
		params: [node.p.type, node.p.radius],
		callback: () => {
			return [paramLabelNumber(node.p.type), paramLabelNumber(node.p.radius)];
		},
	}),
};
