import type {InstanceSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Instance';
import type {MaterialsNetworkSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/MaterialsNetwork';
import {GeoObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/Geo';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

const MAT_NAME = 'MAT';
const MESH_LAMBERT_MAT_NAME = 'meshLambertBuilder_INSTANCES';

function onCreateHook(node: InstanceSopNode) {
	const geoNode = node.parent() as GeoObjNode;

	function createMatNetwork(geoNode: GeoObjNode) {
		const MAT = geoNode.createNode('materialsNetwork');
		MAT.setName(MAT_NAME);

		const nodePos = node.uiData.position();
		MAT.uiData.setPosition(nodePos.x - 200, nodePos.y);

		return MAT;
	}

	const MAT = geoNode.nodesByType('materialsNetwork')[0] || createMatNetwork(geoNode);

	function createMeshMaterial(MAT: MaterialsNetworkSopNode) {
		const mat_node = MAT.createNode('meshLambertBuilder');
		mat_node.setName(MESH_LAMBERT_MAT_NAME);

		const instance_transform1 = mat_node.createNode('instanceTransform');
		const output1 = mat_node.createNode('output');
		const globals = mat_node.createNode('globals');
		globals.uiData.setPosition(-200, 0);
		output1.uiData.setPosition(200, 0);
		output1.setInput('position', instance_transform1, 'position');
		output1.setInput('normal', instance_transform1, 'normal');

		return mat_node;
	}
	const matNode = MAT.node(MESH_LAMBERT_MAT_NAME) || createMeshMaterial(MAT);

	node.p.material.setNode(matNode, {relative: true});
}

export class InstanceSopOnCreateRegister extends SopOnCreateHookRegister<SopType.INSTANCE> {
	override type(): SopType.INSTANCE{
		return SopType.INSTANCE
	}
	override onCreate(node: InstanceSopNode) {
		onCreateHook(node);
	}
}