import {BaseParamType} from '../../../../../../@polygonjs/polygonjs/src/engine/params/_Base';
import {ParamsLabelRegister} from '../BaseSetup';
import {ImageCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/Image';
import {ImageEXRCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/ImageEXR';
import {ImageHDRCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/ImageHDR';
import {ImageKTX2CopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/ImageKTX2';
import {copImageNodeFactoryFactoryResult} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/utils/image/_BaseImage';

function getParams(node: copImageNodeFactoryFactoryResult) {
	let params: BaseParamType[] = [node.p.url];
	params = params.concat(node.textureParamsController.paramLabelsParams());
	return params;
}
function getCallback(node: copImageNodeFactoryFactoryResult) {
	const url = node.p.url.rawInput();
	if (url) {
		const elements = url.split('/');
		const urlLabel = elements[elements.length - 1];
		const textureLabels = node.textureParamsController.paramLabels();
		return [urlLabel].concat(textureLabels);
	} else {
		return '';
	}
}

export const imageCopParamsLabelRegister: ParamsLabelRegister<typeof ImageCopNode, ImageCopNode> = {
	nodeClass: ImageCopNode,
	setupFunc: (node) => {
		return {
			params: getParams(node),
			callback: () => getCallback(node),
		};
	},
};
export const imageEXRCopParamsLabelRegister: ParamsLabelRegister<typeof ImageEXRCopNode, ImageEXRCopNode> = {
	nodeClass: ImageEXRCopNode,
	setupFunc: (node) => {
		return {
			params: getParams(node),
			callback: () => getCallback(node),
		};
	},
};
