import {MouseEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/Mouse';
import {ParamsLabelRegister} from '../BaseSetup';

export const mouseEventParamsLabelRegister: ParamsLabelRegister<typeof MouseEventNode, MouseEventNode> = {
	nodeClass: MouseEventNode,
	setupFunc: (node) => {
		const params = [
			node.p.auxclick,
			node.p.click,
			node.p.dblclick,
			node.p.mousedown,
			node.p.mouseenter,
			node.p.mouseleave,
			node.p.mousemove,
			node.p.mouseout,
			node.p.mouseout,
			node.p.mouseup,
			node.p.pointerlockchange,
			node.p.pointerlockerror,
			node.p.select,
			node.p.wheel,
		];
		return {
			params,
			callback: () =>
				params
					.map((p) => {
						return p.value ? p.name() : undefined;
					})
					.filter((v) => v)
					.join(', '),
		};
	},
};
