import type {ActorSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Actor';
import type{AnimationMixerJsNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/AnimationMixer';
import {Vector2, AnimationMixer, Object3D} from 'three';
import type{AnimationActionJsNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/AnimationAction';
import {JsConnectionPointType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';
import {animationClipsFromAnimationMixer} from '../../../../../../@polygonjs/polygonjs/src/engine/functions/_AnimationMixer';
import type{BaseJsNodeType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/_Base';
import {findOrCreateAnimationMixer} from '../../../../../../@polygonjs/polygonjs/src/engine/functions/_AnimationMixer';
import { JsType } from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Js';
import { JsOnCreateHookRegister } from './_Base';

const translateVector = new Vector2();

//  function animationClipsFromAnimationMixer(animationMixer: AnimationMixer): AnimationClip[] | undefined {
// 	const root = animationMixer.getRoot();
// 	return (root as Object3D).animations;
// }

function outputValueForFirstObject(node: BaseJsNodeType, outputName: string = '') {
	let matchedObject: Object3D | undefined;
	const scene = node.scene();
	const actorsManager = scene.actorsManager;
	const parentNode = node.functionNode(); // actorsManager.parentActorBuilderNode(node);
	scene.threejsScene().traverse((object) => {
		const nodeIds = actorsManager.objectActorNodeIds(object);
		if (!parentNode) {
			return;
		}
		if (!nodeIds) {
			return;
		}
		if (!nodeIds.includes(parentNode.graphNodeId())) {
			return;
		}
		matchedObject = object;
	});
	if (!matchedObject) {
		return;
	}
	return findOrCreateAnimationMixer(matchedObject);
	// return node.outputValue({Object3D: matchedObject}, outputName);
}

export async function createAnimationActionsForAnimationMixer(node: AnimationMixerJsNode) {
	const mixerNodeParent = node.parent() as ActorSopNode;
	await mixerNodeParent.compute();
	if (!mixerNodeParent) {
		console.log('no parent');
		return;
	}
	const mixer = outputValueForFirstObject(node) as AnimationMixer | null;
	if (!mixer) {
		console.log('no mixer');
		return;
	}
	const clips = animationClipsFromAnimationMixer(mixer);
	if (!clips) {
		console.log('no clips');
		return;
	}
	let i = 0;
	const animationActionNodes: AnimationActionJsNode[] = [];
	for (let clip of clips) {
		const animationAction = mixerNodeParent.createNode('animationAction');
		animationAction.setName(`animationAction_${clip.name}`);
		animationAction.p.clipName.set(clip.name);
		translateVector.set(200, i * 150);
		animationAction.uiData.setPosition(node.uiData.position()).translate(translateVector);

		animationAction.setInput(0, node);
		animationActionNodes.push(animationAction);

		i++;
	}
	if (animationActionNodes.length > 0) {
		const switch1 = mixerNodeParent.createNode('switch');
		const switchInputParam = mixerNodeParent.createNode('param');
		switchInputParam.setJsType(JsConnectionPointType.INT);
		switchInputParam.p.name.set('clipIndex');
		switchInputParam.setName('param_switchInputParam');
		switch1.setInput(0, switchInputParam);

		switch1.uiData.setPosition(node.uiData.position()).translate(translateVector.set(500, 0));
		switchInputParam.uiData.setPosition(node.uiData.position()).translate(translateVector.set(300, -100));
		let j = 0;
		for (let animationActionNode of animationActionNodes) {
			switch1.setInput(j + 1, animationActionNode);
			j++;
		}
		const animationActionFadeInManualTrigger = mixerNodeParent.createNode('onManualTrigger');
		const animationActionFadeIn1 = mixerNodeParent.createNode('animationActionFadeIn');
		animationActionFadeIn1.setInput(JsConnectionPointType.TRIGGER, animationActionFadeInManualTrigger);
		animationActionFadeIn1.setInput(JsConnectionPointType.ANIMATION_ACTION, switch1);
		animationActionFadeIn1.uiData.setPosition(node.uiData.position()).translate(translateVector.set(700, 0));
		animationActionFadeInManualTrigger.uiData
			.setPosition(node.uiData.position())
			.translate(translateVector.set(500, -200));
	}
}

function onCreateHook(node: AnimationMixerJsNode) {
	const parent = node.parent() as ActorSopNode;
	if (!parent) {
		return;
	}
	let onEventTick = parent.nodesByType(JsType.ON_TICK)[0];
	if (!onEventTick) {
		onEventTick = parent.createNode(JsType.ON_TICK);
		onEventTick.uiData.setPosition(node.uiData.position());
		translateVector.set(100, -400);
		onEventTick.uiData.translate(translateVector);
	}
	const animationMixerUpdate = parent.createNode('animationMixerUpdate');
	animationMixerUpdate.uiData.setPosition(node.uiData.position());
	translateVector.set(200, -300);
	animationMixerUpdate.uiData.translate(translateVector);

	animationMixerUpdate.setInput(JsConnectionPointType.TRIGGER, onEventTick);
	animationMixerUpdate.setInput(JsConnectionPointType.ANIMATION_MIXER, node);

	node.scene().batchUpdates(async () => {
		await createAnimationActionsForAnimationMixer(node);
	});
}


export class AnimationMixerJsOnCreateHookRegister extends JsOnCreateHookRegister<JsType.ANIMATION_MIXER> {
	override type(): JsType.ANIMATION_MIXER{
		return JsType.ANIMATION_MIXER
	}
	override onCreate(node: AnimationMixerJsNode) {
		onCreateHook(node);
	}
}
