import {paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';
import {CubeCameraSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CubeCamera';

export const cubeCameraSopParamsLabelRegister: ParamsLabelRegister<typeof CubeCameraSopNode, CubeCameraSopNode> = {
	nodeClass: CubeCameraSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [paramLabelNumber(node.p.near), paramLabelNumber(node.p.far), paramLabelNumber(node.p.resolution)];
		},
	}),
};
