import {EditorCoreOnCreateHooksController} from './onCreate/OnCreateHooksController';
import {EditorCoreParamsLabelHooksController} from './paramsLabel/ParamsLabelHooksController';

export class EditorCoreHooksController {
	private static _registered = false;

	public static readonly onCreate = EditorCoreOnCreateHooksController;
	public static readonly paramsLabel = EditorCoreParamsLabelHooksController;

	static registerAll() {
		if (this._registered) {
			return;
		}
		this._registered = true;
		this.onCreate.registerAll();
		this.paramsLabel.registerAll();
	}
}
