import {PointsMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/Points';
import {colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const pointsMatParamsLabelRegister: ParamsLabelRegister<typeof PointsMatNode, PointsMatNode> = {
	nodeClass: PointsMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [colorToParamLabelString(node.pv.color), `size: ${node.pv.size}`],
	}),
};
