import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';
import {CameraMapControlsEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/CameraMapControls';

export const cameraMapControlsEventParamsLabelRegister: ParamsLabelRegister<
	typeof CameraMapControlsEventNode,
	CameraMapControlsEventNode
> = {
	nodeClass: CameraMapControlsEventNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: () => [
				`${booleanAndIcon(node.pv.enabled)} enabled`,
				`${booleanAndIcon(node.pv.allowPan)} pan`,
				`${booleanAndIcon(node.pv.allowRotate)} rotate` +
					(node.pv.allowRotate ? ` (rotateSpeed: ${node.pv.rotateSpeed})` : ``),
				`${booleanAndIcon(node.pv.allowZoom)} zoom`,
				`${booleanAndIcon(node.pv.tdamping)} damping` + (node.pv.tdamping ? ` (${node.pv.damping})` : ``),
			],
		};
	},
};
