import {paramLabelNumber, paramLabelBoolean, ParamsLabelRegister} from '../BaseSetup';
import {Circle3PointsSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Circle3Points';
import {PointsCountMode} from '../../../../../../@polygonjs/polygonjs/src/core/geometry/operation/Circle3Points';

export const circle3PointsSopParamsLabelRegister: ParamsLabelRegister<
	typeof Circle3PointsSopNode,
	Circle3PointsSopNode
> = {
	nodeClass: Circle3PointsSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [
				paramLabelBoolean(node.p.arc),
				paramLabelNumber(node.p.pointsCountMode),
				node.pointsCountMode() == PointsCountMode.SEGMENTS_COUNT
					? paramLabelNumber(node.p.segmentsCount)
					: paramLabelNumber(node.p.segmentsLength),
			];
		},
	}),
};
