import {paramLabelBoolean, ParamsLabelRegister} from '../BaseSetup';
import {MapboxLayerSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/MapboxLayer';
import {ParamType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/ParamType';
import {BooleanParam} from '../../../../../../@polygonjs/polygonjs/src/engine/params/Boolean';

export const mapboxLayerSopParamsLabelRegister: ParamsLabelRegister<typeof MapboxLayerSopNode, MapboxLayerSopNode> = {
	nodeClass: MapboxLayerSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const booleanSpareParams = node.params.spare.filter((param) => param.type() == ParamType.BOOLEAN);
			const toggledParams: BooleanParam[] = booleanSpareParams.filter(
				(param) => (param as BooleanParam).value == true
			) as BooleanParam[];
			return toggledParams.map((p) => paramLabelBoolean(p));
		},
	}),
};
