import {StringParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/String';
import {Vector4Param} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Vector4';
import {Vector3Param} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Vector3';
import {Vector2Param} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Vector2';
import {IntegerParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Integer';
import {FloatParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Float';
import {BooleanParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Boolean';
import {ColorParam} from '../../../../../../@polygonjs/polygonjs/src/engine/params/Color';
import {ConstantJsNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/Constant';
import {
	colorToParamLabelString,
	paramLabelBoolean,
	paramLabelNumber,
	paramLabelString,
	paramLabelVector,
	ParamsLabelRegister,
} from '../BaseSetup';

export const constantJsParamsLabelRegister: ParamsLabelRegister<typeof ConstantJsNode, ConstantJsNode> = {
	nodeClass: ConstantJsNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const param = node.currentParam();
			if (param instanceof ColorParam) {
				return colorToParamLabelString(param.value);
			}
			if (param instanceof BooleanParam) {
				return paramLabelBoolean(param, {displayLabel: false});
			}
			if (param instanceof StringParam) {
				return paramLabelString(param, {displayLabel: false});
			}
			if (param instanceof FloatParam || param instanceof IntegerParam) {
				return paramLabelNumber(param, {displayLabel: false});
			}
			if (param instanceof Vector2Param || param instanceof Vector3Param || param instanceof Vector4Param) {
				return paramLabelVector(param, {displayLabel: false});
			}
			return '';
		},
	}),
};
