import type {ActorPointSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/ActorPoint';
import {JsConnectionPointType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';
import {Vector3Param} from '../../../../../../@polygonjs/polygonjs/src/engine/params/Vector3';

function onCreateHook(node: ActorPointSopNode) {
	const onTick = node.createNode('onTick');
	const setPointPosition = node.createNode('setPointPosition');
	const getPointProperty = node.createNode('getPointProperty');
	const add1 = node.createNode('add');

	setPointPosition.setInput(JsConnectionPointType.TRIGGER, onTick);
	setPointPosition.setInput('position', add1);
	add1.setInput(0, getPointProperty, 'position');
	(add1.params.get('add1')! as Vector3Param).set([0, 0.01, 0]);

	onTick.uiData.setPosition(-100, 0);
	setPointPosition.uiData.setPosition(400, 0);
	add1.uiData.setPosition(200, 100);
	getPointProperty.uiData.setPosition(0, 100);
}

export class ActorPointSopOnCreateRegister extends SopOnCreateHookRegister<SopType.ACTOR_POINT> {
	override type(): SopType.ACTOR_POINT {
		return SopType.ACTOR_POINT;
	}
	override onCreate(node: ActorPointSopNode) {
		onCreateHook(node);
	}
}
