import type {CameraCSSRendererSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CameraCSSRenderer';
import {RopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Rop';
import {SopOnCreateHookRegister} from './_Base';
import {CameraSopNodeType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/NodeContext';

function onCreateHook(node: CameraCSSRendererSopNode) {
	const CSS2DRenderer = node.createNode(RopType.CSS2D);
	node.p.node.setNode(CSS2DRenderer, {relative: true});
}

export class CameraCSSRendererSopOnCreateRegister extends SopOnCreateHookRegister<CameraSopNodeType.CSS_RENDERER> {
	override type(): CameraSopNodeType.CSS_RENDERER {
		return CameraSopNodeType.CSS_RENDERER;
	}
	override onCreate(node: CameraCSSRendererSopNode) {
		onCreateHook(node);
	}
}
