import {AttribRenameSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/AttribRename';
import {ParamsLabelRegister} from '../BaseSetup';

export const attribRenameSopParamsLabelRegister: ParamsLabelRegister<typeof AttribRenameSopNode, AttribRenameSopNode> =
	{
		nodeClass: AttribRenameSopNode,
		setupFunc: (node) => ({
			params: [node.p.oldName, node.p.newName],
			callback: () => {
				if (node.pv.oldName != '' && node.pv.newName != '') {
					return `${node.pv.oldName} -> ${node.pv.newName}`;
				} else {
					return '';
				}
			},
		}),
	};
