import {BaseParamType} from '../../../../../../@polygonjs/polygonjs/src/engine/params/_Base';
import {KeyboardEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/Keyboard';
import {ParamsLabelRegister} from '../BaseSetup';

export const keyboardEventParamsLabelRegister: ParamsLabelRegister<typeof KeyboardEventNode, KeyboardEventNode> = {
	nodeClass: KeyboardEventNode,
	setupFunc: (node) => {
		const eventParams: BaseParamType[] = [node.p.keydown, node.p.keypress, node.p.keyup];
		return {
			params: eventParams.concat([node.p.keyCodes]),
			callback: () => {
				const eventNames = eventParams
					.map((p) => {
						return p.value ? p.name() : undefined;
					})
					.filter((v) => v)
					.join(', ');

				return `${eventNames} (${node.pv.keyCodes})`;
			},
		};
	},
};
