import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';
import {HexagonsSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Hexagons';

export const hexagonsSopParamsLabelRegister: ParamsLabelRegister<typeof HexagonsSopNode, HexagonsSopNode> = {
	nodeClass: HexagonsSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list = [`direction: ${node.pv.direction.toArray()}`, `size: ${node.pv.size.toArray()}`];

			list.push(`${booleanAndIcon(node.pv.pointsOnly)} pointsOnly`);

			return list;
		},
	}),
};
