import {AttribIdSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/AttribId';
import {booleanAndIcon, paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';

export const attribIdSopParamsLabelRegister: ParamsLabelRegister<typeof AttribIdSopNode, AttribIdSopNode> = {
	nodeClass: AttribIdSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [
				paramLabelNumber(node.p.class),
				`${booleanAndIcon(node.pv.id)}: ${node.pv.idName}`,
				`${booleanAndIcon(node.pv.idn)}: ${node.pv.idnName}`,
			];
		},
	}),
};
