import {LineBasicMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/LineBasic';
import {colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const lineBasicMatParamsLabelRegister: ParamsLabelRegister<typeof LineBasicMatNode, LineBasicMatNode> = {
	nodeClass: LineBasicMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [colorToParamLabelString(node.pv.color), `lineWidth: ${node.pv.lineWidth}`],
	}),
};
