import {FileAudioNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/audio/File';
import {ParamsLabelRegister} from '../BaseSetup';

export const fileAudioParamsLabelRegister: ParamsLabelRegister<typeof FileAudioNode, FileAudioNode> = {
	nodeClass: FileAudioNode,
	setupFunc: (node) => {
		return {
			params: [node.p.url],
			callback: () => {
				const url = node.p.url.rawInput();
				const elements = url.split('/');
				return elements[elements.length - 1];
			},
		};
	},
};
