import {SceneEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/Scene';
import {ParamsLabelRegister} from '../BaseSetup';

export const sceneEventParamsLabelRegister: ParamsLabelRegister<typeof SceneEventNode, SceneEventNode> = {
	nodeClass: SceneEventNode,
	setupFunc: (node) => {
		const params = [node.p.created, node.p.ready, node.p.play, node.p.pause, node.p.tick];
		return {
			params,
			callback: () =>
				params
					.map((p) => {
						return p.value ? p.name() : undefined;
					})
					.filter((v) => v)
					.join(', '),
		};
	},
};
