import type {PointLightSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PointLight';
import {SopOnCreateHookRegister} from './_Base';
import {LightType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Light';

function onCreateHook(node: PointLightSopNode) {
	node.p.showHelper.set(true);
}

export class PointLightSopOnCreateRegister extends SopOnCreateHookRegister<LightType.POINT> {
	override type(): LightType.POINT {
		return LightType.POINT;
	}
	override onCreate(node: PointLightSopNode) {
		onCreateHook(node);
	}
}
