import {paramLabelNumber, paramLabelVector3, ParamsLabelRegister} from '../BaseSetup';
import {BoxSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Box';

export const boxSopParamsLabelRegister: ParamsLabelRegister<typeof BoxSopNode, BoxSopNode> = {
	nodeClass: BoxSopNode,
	setupFunc: (node) => ({
		params: [node.p.sizes, node.p.size],
		callback: () => {
			return [paramLabelVector3(node.p.sizes), paramLabelNumber(node.p.size)];
		},
	}),
};
