import {MeshStandardBuilderMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshStandardBuilder';
import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';

export const meshStandardBuilderMatParamsLabelRegister: ParamsLabelRegister<
	typeof MeshStandardBuilderMatNode,
	MeshStandardBuilderMatNode
> = {
	nodeClass: MeshStandardBuilderMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			`${booleanAndIcon(node.pv.useEnvMap)} useEnvMap` + (node.pv.useEnvMap ? `: ${node.pv.envMap.path()}` : ``),
			`roughness: ${node.pv.roughness}`,
			`metalness: ${node.pv.metalness}`,
		],
	}),
};
