import {QuadTriangulateSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/QuadTriangulate';
import {ParamsLabelRegister, paramLabelBoolean} from '../BaseSetup';

export const quadTriangulateSopParamsLabelRegister: ParamsLabelRegister<
	typeof QuadTriangulateSopNode,
	QuadTriangulateSopNode
> = {
	nodeClass: QuadTriangulateSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			paramLabelBoolean(node.p.triangles),
			paramLabelBoolean(node.p.wireframe),
			paramLabelBoolean(node.p.center),
		],
	}),
};
