import {
	ParamsLabelResult,
	paramLabelBoolean,
	paramLabelString,
	ParamsLabelRegister,
	paramLabelNumber,
} from './../BaseSetup';
import {WebGLRendererRopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/rop/WebGLRenderer';

export const webGLRendererRopParamsLabelRegister: ParamsLabelRegister<
	typeof WebGLRendererRopNode,
	WebGLRendererRopNode
> = {
	nodeClass: WebGLRendererRopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];

			list.push(paramLabelBoolean(node.p.antialias));
			list.push(paramLabelNumber(node.p.toneMapping));
			list.push(paramLabelString(node.p.outputColorSpace));
			list.push(paramLabelBoolean(node.p.alpha));
			list.push(paramLabelBoolean(node.p.sortObjects));

			return list;
		},
	}),
};
