export type ParamLabelResultBasic = string;
export interface ParamLabelResultColoredText {
	label: StringOrNumber;
	className: string;
	bold?: boolean;
}
export interface ParamLabelResultDetailed {
	detailed: boolean;
	elements: Array<string | ParamLabelResultColoredText>;
}
export interface ParamLabelResultColor {
	color: string;
}
export type ParamsLabelResult = ParamLabelResultBasic | ParamLabelResultColor | ParamLabelResultDetailed;
type ParamsLabelCallback = () =>
	| ParamsLabelResult
	| ParamsLabelResult[]
	| Promise<ParamsLabelResult | ParamsLabelResult[]>;
interface ParamsLabelConfig {
	params: BaseParamType | BaseParamType[];
	callback: ParamsLabelCallback;
}
export type ParamsLabelSetup<N extends BaseNodeClass> = (node: N) => ParamsLabelConfig;

// export type ParamsLabelRegister<N extends typeof BaseNodeClass, NI extends BaseNodeClass> = (
// 	node: N
// ) => ParamsLabelSetup<NI>;

export interface ParamsLabelRegister<N extends typeof BaseNodeClass, NI extends BaseNodeClass> {
	nodeClass: N;
	setupFunc: ParamsLabelSetup<NI>;
}

// export const PARAM_LABEL_RESULT_COLOR_PREFIX = 'rgb-';
export function colorToParamLabelString(color: Color) {
	return {color: color.getStyle()};
	// return `${PARAM_LABEL_RESULT_COLOR_PREFIX}${color.getStyle()}`;
}

