import {FloatParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Float';
import {IntegerParam} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Integer';
import {ArrayUtils} from './../../../../../../@polygonjs/polygonjs/src/core/ArrayUtils';
import {CssType, typeClassName} from './../../../ColorUtils';
import {
	NumericParamOptionsMenuEntry,
	StringParamOptionsMenuEntry,
} from '../../../../../../@polygonjs/polygonjs/src/engine/params/utils/OptionsController';
import {
	ParamLabelOptions,
	ParamsLabelResult,
	EXPRESSION_CLASS,
	MENU_ENTRIES_JS_TYPE,
	COLOR_STRENGTH,
	DEFAULT_CLASS,
	ParamLabelResultColoredText,
} from './_Base';

export function _paramLabelNumber(param: FloatParam | IntegerParam, options?: ParamLabelOptions): ParamsLabelResult {
	const label = options?.displayLabel == false ? undefined : `${param.name()}:`;
	const compactElements: Array<string | ParamLabelResultColoredText> = [];
	if (param.hasExpression()) {
		ArrayUtils.compact(
			[
				label,
				{
					label: param.rawInput(),
					className: EXPRESSION_CLASS,
					bold: true,
				},
				// not sure that the result should be displayed, as it would not make sense when using a copy node for instance.
				// nor when using params which are per entity, but that could be detected.
				// maybe it is possible to detect when using a copy?
				// '(=',
				// {
				// 	label: param.value,
				// 	className: EXPRESSION_RESULT,
				// },
				// ')',
			],
			compactElements
		);
		return {
			detailed: true,
			elements: compactElements,
		};
		// return `${param.name()}: ${param.rawInput()} (=${param.value})`;
	} else {
		if (param.options.hasMenu()) {
			const availableEntries = param.options.menuEntries();
			const selectedEntry =
				availableEntries.find(
					(entry: NumericParamOptionsMenuEntry | StringParamOptionsMenuEntry) => entry.value == param.value
				) || availableEntries[0];
			ArrayUtils.compact(
				[
					label,
					{
						label: selectedEntry.name,
						className:
							options?.className ||
							typeClassName(MENU_ENTRIES_JS_TYPE[param.value], CssType.TEXT, COLOR_STRENGTH) ||
							DEFAULT_CLASS,
					},
				],
				compactElements
			);
			return {
				detailed: true,
				elements: compactElements,
			};
		} else {
			const className = options?.className;
			if (className) {
				ArrayUtils.compact(
					[
						label,
						{
							label: param.rawInput(),
							className,
						},
					],
					compactElements
				);
				return {
					detailed: true,
					elements: compactElements,
				};
			} else {
				const compactElements2: Array<string | number> = [];
				ArrayUtils.compact([label, param.rawInput()], compactElements2);
				return compactElements2.join(' ');
			}
		}
	}
}
