import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';
import {FirstPersonControlsEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/FirstPersonControls';

export const firstPersonControlsEventParamsLabelRegister: ParamsLabelRegister<
	typeof FirstPersonControlsEventNode,
	FirstPersonControlsEventNode
> = {
	nodeClass: FirstPersonControlsEventNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: () => [
				node.pv.colliderObject.path(),
				`capsuleRadius: ${node.pv.capsuleRadius}`,
				`capsuleHeight: ${node.pv.capsuleHeight}`,
				`${booleanAndIcon(node.pv.jumpAllowed)} jump` +
					(node.pv.jumpAllowed ? ` (strength: ${node.pv.jumpStrength})` : ``),
				`${booleanAndIcon(node.pv.runAllowed)} run` +
					(node.pv.runAllowed ? ` (speedMult: ${node.pv.runSpeedMult})` : ``),
			],
		};
	},
};
