import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {ObjectPropertiesSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/ObjectProperties';
import {
	ParamsLabelRegister,
	paramLabelString,
	paramLabelNumber,
	paramLabelBoolean,
	ParamsLabelResult,
} from '../BaseSetup';

export const objectPropertiesSopParamsLabelRegister: ParamsLabelRegister<
	typeof ObjectPropertiesSopNode,
	ObjectPropertiesSopNode
> = {
	nodeClass: ObjectPropertiesSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const elements: ParamsLabelResult[] = [];
			if (node.pv.group.trim().length > 0) {
				elements.push(paramLabelString(node.p.group));
			}
			if (isBooleanTrue(node.pv.tname)) {
				elements.push(paramLabelString(node.p.name));
			}
			if (isBooleanTrue(node.pv.trenderOrder)) {
				elements.push(paramLabelNumber(node.p.renderOrder));
			}
			if (isBooleanTrue(node.pv.tfrustumCulled)) {
				elements.push(paramLabelBoolean(node.p.frustumCulled));
			}
			if (isBooleanTrue(node.pv.tmatrixAutoUpdate)) {
				elements.push(paramLabelBoolean(node.p.matrixAutoUpdate));
			}
			if (isBooleanTrue(node.pv.tvisible)) {
				elements.push(paramLabelBoolean(node.p.visible));
			}
			if (isBooleanTrue(node.pv.tcastShadow)) {
				elements.push(paramLabelBoolean(node.p.castShadow));
			}
			if (isBooleanTrue(node.pv.treceiveShadow)) {
				elements.push(paramLabelBoolean(node.p.receiveShadow));
			}
			return elements;
		},
	}),
};
