import type {MeshStandardBuilderMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshStandardBuilder';
import type {SubnetInputGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/SubnetInput';
import type {SubnetOutputGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/SubnetOutput';
import type {Vec4ToVec3GlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/_ConversionVecTo';
import type {ComputeNormalsGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/ComputeNormals';
import {ComputeNormalsInput} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/ComputeNormals';
import {GlConnectionPointType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Gl';
import type {Vector2Param} from '../../../../../../@polygonjs/polygonjs/src/engine/params/Vector2';
import type {NodePathParam} from '../../../../../../@polygonjs/polygonjs/src/engine/params/NodePath';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {GlOnCreateHookRegister} from './_Base';

const TEXTURE_PARAM_NAME = 'textureAttribute';
const TEXTURE_SIZE_PARAM_NAME = 'textureSize';

interface OnCreateHookReturn {
	subnetInput1: SubnetInputGlNode;
	subnetOutput1: SubnetOutputGlNode;
	vec4ToVec3_1: Vec4ToVec3GlNode;
}

export function computeNormalsGlOnCreate(node: ComputeNormalsGlNode): Promise<OnCreateHookReturn> {
	return new Promise((resolve) => {
		node.p.inputsCount.set(1);
		const subnetInput1 = node.createNode('subnetInput');
		const subnetOutput1 = node.createNode('subnetOutput');
		const texture1 = node.createNode('texture');
		const vec4ToVec3_1 = node.createNode('vec4ToVec3');

		texture1.p.paramName.set(TEXTURE_PARAM_NAME);

		subnetInput1.uiData.setPosition(-300, 0);
		texture1.uiData.setPosition(-100, 0);
		vec4ToVec3_1.uiData.setPosition(+100, 0);
		subnetOutput1.uiData.setPosition(+300, 0);

		subnetOutput1.setInput(ComputeNormalsInput.P, vec4ToVec3_1, 0);
		vec4ToVec3_1.setInput(0, texture1);
		texture1.setInput(0, subnetInput1, ComputeNormalsInput.UV);

		const parentNode = node.parent() as MeshStandardBuilderMatNode;
		const _findTextureSizeParam = () => {
			const paramNodes = parentNode.nodesByType('param');
			if (!paramNodes) {
				return;
			}
			return paramNodes
				.filter((paramNode) => paramNode.glType() == GlConnectionPointType.VEC2)
				.filter((paramNode) => paramNode.name() == 'textureSize')[0];
		};
		const _createTextureSizeParam = () => {
			const param = parentNode.createNode('param');
			param.p.name.set(TEXTURE_SIZE_PARAM_NAME);
			param.setGlType(GlConnectionPointType.VEC2);
			// param.p.defaultValue.set([512,512])
			param.setName('param_textureSize');

			param.uiData.setPosition(node.uiData.position().x - 100, node.uiData.position().y + 200);
			return param;
		};

		const paramTextureSize = _findTextureSizeParam() || _createTextureSizeParam();
		node.setInput(ComputeNormalsInput.TEXTURE_SIZE, paramTextureSize);

		const setTextureSizeParamExpression = () => {
			const textureNodeParam = parentNode.params.spare.find(
				(param) => param.name() == TEXTURE_PARAM_NAME
			) as NodePathParam;
			const textureSizeParam = parentNode.params.spare.find(
				(param) => param.name() == TEXTURE_SIZE_PARAM_NAME
			) as Vector2Param;
			if (!textureNodeParam) {
				return;
			}
			if (!textureSizeParam) {
				return;
			}

			// find the first geometryAttribute node available
			const scene = node.scene();
			const geometryAttributeNode = scene.nodesController.nodesByType('geometryAttribute')[0];
			if (geometryAttributeNode) {
				textureNodeParam.setNode(geometryAttributeNode);
			}

			textureSizeParam.set([
				`copRes(chsop('${textureNodeParam.name()}')).x`,
				`copRes(chsop('${textureNodeParam.name()}')).y`,
			]);

			const returnValue: OnCreateHookReturn = {
				subnetInput1,
				subnetOutput1,
				vec4ToVec3_1,
			};
			resolve(returnValue);
		};

		setTimeout(setTextureSizeParamExpression, 100);
	});
}

export class ComputeNormalsGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.COMPUTE_NORMALS> {
	override type(): GlType.COMPUTE_NORMALS {
		return GlType.COMPUTE_NORMALS;
	}
	override onCreate(node: ComputeNormalsGlNode) {
		computeNormalsGlOnCreate(node);
	}
}
