import {AddSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Add';
import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';

export const addSopParamsLabelRegister: ParamsLabelRegister<typeof AddSopNode, AddSopNode> = {
	nodeClass: AddSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			`${booleanAndIcon(node.pv.createPoint)} createPoints`,
			`${node.pv.pointsCount} ${node.pv.pointsCount == 1 ? 'point' : 'points'}`,
			`${booleanAndIcon(node.pv.connectInputPoints)} connectInputPoints`,
			`${booleanAndIcon(node.pv.connectToLastPoint)} connectToLastPoint`,
		],
	}),
};
