import {LayerSopNode, UPDATE_MODES} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Layer';
import {ParamsLabelRegister, ParamsLabelResult} from '../BaseSetup';

export const layerSopParamsLabelRegister: ParamsLabelRegister<typeof LayerSopNode, LayerSopNode> = {
	nodeClass: LayerSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];

			const layersCount = node.pv.layersCount;

			if (layersCount >= 1) {
				const updateMode = UPDATE_MODES[node.pv.updateMode0];
				list.push(`${updateMode}: ${node.pv.layer0}`);
			}
			if (layersCount >= 2) {
				const updateMode = UPDATE_MODES[node.pv.updateMode1];
				list.push(`${updateMode}: ${node.pv.layer1}`);
			}
			if (layersCount >= 3) {
				const updateMode = UPDATE_MODES[node.pv.updateMode2];
				list.push(`${updateMode}: ${node.pv.layer2}`);
			}
			if (layersCount >= 4) {
				const updateMode = UPDATE_MODES[node.pv.updateMode3];
				list.push(`${updateMode}: ${node.pv.layer3}`);
			}

			return list;
		},
	}),
};
