import {AttribPromoteSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/AttribPromote';
import {ATTRIB_PROMOTE_MODES} from '../../../../../../@polygonjs/polygonjs/src/engine/operations/sop/AttribPromote';
import {ParamsLabelRegister, ParamsLabelResult, paramLabelString} from '../BaseSetup';
import {AttribClassMenuEntries} from '../../../../../../@polygonjs/polygonjs/src/core/geometry/Constant';

export const attribPromoteSopParamsLabelRegister: ParamsLabelRegister<
	typeof AttribPromoteSopNode,
	AttribPromoteSopNode
> = {
	nodeClass: AttribPromoteSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];

			if (node.pv.group != '') {
				list.push(paramLabelString(node.p.group));
			}
			if (node.pv.name != '') {
				const fromS = AttribClassMenuEntries.filter((entry) => entry.value == node.pv.classFrom)[0].name;
				const toS = AttribClassMenuEntries.filter((entry) => entry.value == node.pv.classTo)[0].name;
				const mode = ATTRIB_PROMOTE_MODES[node.pv.mode];
				list.push(`${node.pv.name} (${fromS} -> ${toS}, ${mode})`);
			}

			return list;
		},
	}),
};
