import {paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';
import {MapboxTransformSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/MapboxTransform';

export const mapboxTransformSopParamsLabelRegister: ParamsLabelRegister<
	typeof MapboxTransformSopNode,
	MapboxTransformSopNode
> = {
	nodeClass: MapboxTransformSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [
				paramLabelNumber(node.p.applyOn),
				paramLabelNumber(node.p.longitude),
				paramLabelNumber(node.p.latitude),
			];
		},
	}),
};
