import {FileSVGSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/FileSVG';
import {ParamsLabelRegister} from '../BaseSetup';

export const fileSVGSopParamsLabelRegister: ParamsLabelRegister<typeof FileSVGSopNode, FileSVGSopNode> = {
	nodeClass: FileSVGSopNode,
	setupFunc: (node) => ({
		params: node.p.url,
		callback: () => {
			const url = node.pv.url;
			if (url) {
				const elements = url.split('/');
				return elements[elements.length - 1];
			} else {
				return '';
			}
		},
	}),
};
