import {CADThicknessSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CADThickness';
import {paramLabelString, paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';

export const cadThicknessSopParamsLabelRegister: ParamsLabelRegister<typeof CADThicknessSopNode, CADThicknessSopNode> =
	{
		nodeClass: CADThicknessSopNode,
		setupFunc: (node) => ({
			params: node.params.all,
			callback: () => {
				return [paramLabelString(node.p.facesGroupToDelete), paramLabelNumber(node.p.offset)];
			},
		}),
	};
