import {ParamsLabelRegister, ParamsLabelResult, colorToParamLabelString} from '../BaseSetup';
import {ColorSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Color';
import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';

export const colorSopParamsLabelRegister: ParamsLabelRegister<typeof ColorSopNode, ColorSopNode> = {
	nodeClass: ColorSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];
			if (isBooleanTrue(node.pv.fromAttribute)) {
				list.push(`from attribute: ${node.pv.attribName}`);
			} else {
				list.push(colorToParamLabelString(node.pv.color));
			}

			return list;
		},
	}),
};
