import type {DirectionalLightObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/DirectionalLight';
import {ObjOnCreateHookRegister} from './_Base';
import {LightType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Light';

function onCreateHook(node: DirectionalLightObjNode) {
	node.p.showHelper.set(true);
}

export class DirectionalLightObjOnCreateRegister extends ObjOnCreateHookRegister<LightType.DIRECTIONAL> {
	override type(): LightType.DIRECTIONAL {
		return LightType.DIRECTIONAL;
	}
	override onCreate(node: DirectionalLightObjNode) {
		onCreateHook(node);
	}
}
