import {ParamsLabelRegister} from '../BaseSetup';
import {CapsuleSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Capsule';

export const capsuleSopParamsLabelRegister: ParamsLabelRegister<typeof CapsuleSopNode, CapsuleSopNode> = {
	nodeClass: CapsuleSopNode,
	setupFunc: (node) => ({
		params: [node.p.radius, node.p.height],
		callback: () => {
			return [`radius: ${node.pv.radius}`, `height: ${node.pv.height}`];
		},
	}),
};
