import {CoreSleep} from '../../../../../../@polygonjs/polygonjs/src/core/Sleep';
import {CoreType} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {PropertyValueAnimNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/anim/PropertyValue';
import {colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const propertyValueAnimParamsLabelRegister: ParamsLabelRegister<
	typeof PropertyValueAnimNode,
	PropertyValueAnimNode
> = {
	nodeClass: PropertyValueAnimNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: async () => {
				// I need a sleep here for now
				// to make sure the node is set dirty after a param update
				await CoreSleep.sleep(100);
				const container = await node.compute();
				const timelineBuilder = container.timelineBuilder();
				const value = timelineBuilder.propertyValue();
				if (CoreType.isNumber(value)) {
					return `${value}`;
				}
				if (CoreType.isVector(value)) {
					return `${value.toArray().join(',')}`;
				}
				if (CoreType.isColor(value)) {
					return colorToParamLabelString(value);
				}
				if (CoreType.isQuaternion(value)) {
					return `${value.toArray().join(',')}`;
				}
				return '';
			},
		};
	},
};
