import {PostProcessNetworkAnimNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/anim/PostProcessNetwork';
import {PostProcessNetworkCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/PostProcessNetwork';
import {PostProcessNetworkEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/PostProcessNetwork';
import {PostProcessNetworkMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/PostProcessNetwork';
import {PostProcessNetworkObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/PostProcessNetwork';
import {PostProcessNetworkPostNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/post/PostProcessNetwork';
import {PostProcessNetworkRopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/rop/PostProcessNetwork';
import {PostProcessNetworkSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PostProcessNetwork';
import {CameraPostProcessSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CameraPostProcess';
import {ParamsLabelRegister, booleanAndIcon} from '../BaseSetup';
import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {postProcessTextureTypeLabel} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/post/utils/EffectComposerController';

type PostProcessNetworkNode =
	| PostProcessNetworkAnimNode
	| PostProcessNetworkCopNode
	| PostProcessNetworkEventNode
	| PostProcessNetworkMatNode
	| PostProcessNetworkObjNode
	| PostProcessNetworkPostNode
	| PostProcessNetworkRopNode
	| PostProcessNetworkSopNode
	| CameraPostProcessSopNode;

export const POST_PROCESS_NETWORK_NODES = [
	PostProcessNetworkAnimNode,
	PostProcessNetworkCopNode,
	PostProcessNetworkEventNode,
	PostProcessNetworkMatNode,
	PostProcessNetworkObjNode,
	PostProcessNetworkPostNode,
	PostProcessNetworkRopNode,
	PostProcessNetworkSopNode,
	CameraPostProcessSopNode,
];
export const postProcessNetworksRegisters: Array<ParamsLabelRegister<any, any>> = POST_PROCESS_NETWORK_NODES.map(
	(nodeClass) => {
		const paramsLabelRegister: ParamsLabelRegister<typeof nodeClass, CameraPostProcessSopNode> = {
			nodeClass,
			setupFunc: (node: PostProcessNetworkNode) => ({
				params: node.params.all,
				callback: () => {
					const elements: string[] = [];
					elements.push(`prependRenderPass: ${booleanAndIcon(node.pv.prependRenderPass)}`);
					elements.push(`depthBuffer: ${booleanAndIcon(node.pv.depthBuffer)}`);
					elements.push(`stencilBuffer: ${booleanAndIcon(node.pv.stencilBuffer)}`);
					elements.push(`sampling: ${node.pv.sampling}`);
					const textureTypeLabel = isBooleanTrue(node.pv.tTextureType)
						? postProcessTextureTypeLabel(node.pv.textureType)
						: 'default';
					elements.push(`textureType: ${textureTypeLabel}`);

					return elements;
				},
			}),
		};
		return paramsLabelRegister;
	}
);
