import {ObjectVariable} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/code/assemblers/objectBuilder/ObjectBuilderAssemblerCommon';
import type {ObjectBuilderSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/ObjectBuilder';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

function onCreateHook(node: ObjectBuilderSopNode) {
	const globals = node.createNode('globals');
	const output1 = node.createNode('output');
	const add1 = node.createNode('add');

	add1.setInput(0, globals, ObjectVariable.POSITION);
	output1.setInput(ObjectVariable.POSITION, add1);

	// add1.p.radius.set(0.4);

	globals.uiData.setPosition(-400, 0);
	output1.uiData.setPosition(0, 0);
	add1.uiData.setPosition(-200, 0);
}

export class ObjectBuilderSopOnCreateRegister extends SopOnCreateHookRegister<SopType.OBJECT_BUILDER> {
	override type(): SopType.OBJECT_BUILDER {
		return SopType.OBJECT_BUILDER;
	}
	override onCreate(node: ObjectBuilderSopNode) {
		onCreateHook(node);
	}
}
