import type {SDFGradientGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/SDFGradient';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {GlOnCreateHookRegister} from './_Base';

export function onCreateHook(node: SDFGradientGlNode) {
	node.p.inputsCount.set(0);

	const subnetInput1 = node.createNode('subnetInput');
	const subnetOutput1 = node.createNode('subnetOutput');
	const SDFSphere1 = node.createNode('SDFSphere');

	subnetOutput1.setInput(0, SDFSphere1);
	SDFSphere1.setInput(0, subnetInput1);

	subnetInput1.uiData.setPosition(-200, 0);
	SDFSphere1.uiData.setPosition(0, 0);
	subnetOutput1.uiData.setPosition(+200, 0);
}

export class SDFGradientGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.SDF_GRADIENT> {
	override type(): GlType.SDF_GRADIENT {
		return GlType.SDF_GRADIENT;
	}
	override onCreate(node: SDFGradientGlNode) {
		onCreateHook(node);
	}
}
