import type {ActorSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Actor';
import {JsConnectionPointType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';
import {OnTickJsNodeOuput} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/OnTick';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

function onCreateHook(node: ActorSopNode) {
	const onTick = node.createNode('onTick');
	const setObjectPosition = node.createNode('setObjectPosition');
	const floatToVec3 = node.createNode('floatToVec3');
	const sin = node.createNode('sin');

	setObjectPosition.setInput(JsConnectionPointType.TRIGGER, onTick);
	setObjectPosition.setInput('position', floatToVec3);
	sin.setInput(0, onTick, OnTickJsNodeOuput.TIME);
	floatToVec3.setInput('y', sin);

	onTick.uiData.setPosition(-100, 0);
	setObjectPosition.uiData.setPosition(400, 0);
	floatToVec3.uiData.setPosition(200, 100);
	sin.uiData.setPosition(100, 100);
}

export class ActorSopOnCreateRegister extends SopOnCreateHookRegister<SopType.ACTOR> {
	override type(): SopType.ACTOR {
		return SopType.ACTOR;
	}
	override onCreate(node: ActorSopNode) {
		onCreateHook(node);
	}
}
