import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {BlockEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/Block';
import {ParamsLabelRegister} from '../BaseSetup';

export const blockEventParamsLabelRegister: ParamsLabelRegister<typeof BlockEventNode, BlockEventNode> = {
	nodeClass: BlockEventNode,
	setupFunc: (node) => ({
		params: node.p.blocking,
		callback: () => (isBooleanTrue(node.pv.blocking) ? 'blocking (X)' : 'pass-through (-->)'),
	}),
};
