import {PointVariable} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/code/assemblers/pointBuilder/PointBuilderAssemblerCommon';
import type {PointBuilderSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PointBuilder';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';

function onCreateHook(node: PointBuilderSopNode) {
	const globals = node.createNode('globals');
	const output1 = node.createNode('output');
	const add1 = node.createNode('add');

	add1.setInput(0, globals, PointVariable.POSITION);
	output1.setInput(PointVariable.POSITION, add1);

	// add1.p.radius.set(0.4);

	globals.uiData.setPosition(-400, 0);
	output1.uiData.setPosition(0, 0);
	add1.uiData.setPosition(-200, 0);
}

export class PointBuilderSopOnCreateRegister extends SopOnCreateHookRegister<SopType.POINT_BUILDER> {
	override type(): SopType.POINT_BUILDER {
		return SopType.POINT_BUILDER;
	}
	override onCreate(node: PointBuilderSopNode) {
		onCreateHook(node);
	}
}
