import {ParamsLabelResult} from './../BaseSetup';
import {ActorInstanceSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/ActorInstance';
import {ParamsLabelRegister, paramLabelString, paramLabelNodePath} from '../BaseSetup';

export const actorInstanceSopParamsLabelRegister: ParamsLabelRegister<
	typeof ActorInstanceSopNode,
	ActorInstanceSopNode
> = {
	nodeClass: ActorInstanceSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];

			const {group, useThisNode} = node.pv;
			if (group.trim() != '') {
				list.push(paramLabelString(node.p.group));
			}

			if (!useThisNode) {
				list.push(paramLabelNodePath(node.p.node));
			}

			return list;
		},
	}),
};
