import type {CameraRendererSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CameraRenderer';
import {RopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Rop';
import {SopOnCreateHookRegister} from './_Base';
import {CameraSopNodeType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/NodeContext';

export function onCreateHook(node: CameraRendererSopNode) {
	const webglRenderer = node.createNode(RopType.WEBGL);
	node.p.node.setNode(webglRenderer, {relative: true});

	return {webglRenderer};
}

export class CameraRendererSopOnCreateRegister extends SopOnCreateHookRegister<CameraSopNodeType.RENDERER> {
	override type(): CameraSopNodeType.RENDERER {
		return CameraSopNodeType.RENDERER;
	}
	override onCreate(node: CameraRendererSopNode) {
		onCreateHook(node);
	}
}
