import type {PositionalAudioObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/PositionalAudio';
import {Vector2} from 'three';
import {CoreWalker} from '../../../../../../@polygonjs/polygonjs/src/core/Walker';
import type {AudioListenerObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/AudioListener';
import {Poly} from '../../../../../../@polygonjs/polygonjs/src/engine/Poly';
import {BaseCameraObjNodeType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/_BaseCamera';
import {ObjOnCreateHookRegister} from './_Base';
import {ObjType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Obj';

const pos = new Vector2();

async function onCreateHook(node: PositionalAudioObjNode) {
	const envelope1 = node.createNode('envelope');
	const AMSynth1 = node.createNode('AMSynth');
	const playInstrument1 = node.createNode('playInstrument');
	const null1 = node.createNode('null');

	null1.setName('OUT');
	null1.setInput(0, playInstrument1);
	playInstrument1.setInput(0, AMSynth1);
	AMSynth1.setInput(0, envelope1);

	pos.copy(null1.uiData.position());
	pos.y -= 100;
	playInstrument1.uiData.setPosition(pos);
	pos.y -= 100;
	AMSynth1.uiData.setPosition(pos);
	pos.y -= 100;
	envelope1.uiData.setPosition(pos);

	node.p.audioNode.set(CoreWalker.relativePath(node, null1));

	let listener: AudioListenerObjNode | undefined = node.root().audioController.audioListeners()[0];
	if (!listener) {
		listener = node.root().createNode('audioListener');
		// now that the cameras can be at the geometry level,
		// we cannot just take the mainCameraNode and assume it will be at the obj level.
		// so we:
		// - take the obj level cameras
		// - if there is one matching the mainCameraPath, we take it,
		// - if not, we take any.
		const scene = node.scene();
		const cameraNodeTypes = Poly.camerasRegister.registeredNodeTypes();
		const objCameraNodes = cameraNodeTypes.map((type) => scene.nodesByType(type)).flat() as BaseCameraObjNodeType[];
		const mainCamera = await scene.camerasController.mainCamera();
		const cameraNode =
			objCameraNodes.find((cameraNode) => {
				return cameraNode.object == mainCamera;
			}) || objCameraNodes[0];
		// const cameraNode = node.scene().mainCameraNode();
		if (cameraNode) {
			listener.setInput(0, cameraNode);
			pos.copy(cameraNode.uiData.position());
			pos.y += 200;
			listener.uiData.setPosition(pos);
		}
	}
}
export class PositionalAudioObjOnCreateRegister extends ObjOnCreateHookRegister<ObjType.POSITIONAL_AUDIO> {
	override type(): ObjType.POSITIONAL_AUDIO {
		return ObjType.POSITIONAL_AUDIO;
	}
	override onCreate(node: PositionalAudioObjNode) {
		onCreateHook(node);
	}
}
