const CONTEXTS: NodeContext[] = [
	NodeContext.ANIM,
	NodeContext.AUDIO,
	NodeContext.COP,
	NodeContext.EVENT,
	NodeContext.MAT,
	NodeContext.OBJ,
	NodeContext.POST,
	NodeContext.ROP,
	NodeContext.SOP,
];
