export const ActorNetworkOnCreateHookRegisters = CONTEXTS.map((context) => {
	class ActorNetworkOnCreateRegister extends BaseOnCreateHookRegister<NodeContext, NetworkNodeType.ACTOR> {
		override context(): NodeContext {
			return context;
		}
		override type(): NetworkNodeType.ACTOR {
			return NetworkNodeType.ACTOR;
		}
		override onCreate(node: any) {
			onCreateHook(node);
		}
	}
	return ActorNetworkOnCreateRegister;
});
