import {TransformTargetType} from '../../../../../../@polygonjs/polygonjs/src/core/Transform';
import {TransformSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Transform';
import {paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';

export const transformSopParamsLabelRegister: ParamsLabelRegister<typeof TransformSopNode, TransformSopNode> = {
	nodeClass: TransformSopNode,
	setupFunc: (node) => ({
		params: [node.p.applyOn, node.p.objectMode],
		callback: () => {
			const applyOnParamLabel = paramLabelNumber(node.p.applyOn);
			const list = [applyOnParamLabel];
			if (node.applyOn() == TransformTargetType.OBJECT) {
				list.push(paramLabelNumber(node.p.objectMode));
			}
			return list;
		},
	}),
};
