import {AudioNotesSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/AudioNotes';
import {paramLabelNumber, paramLabelString, ParamsLabelRegister} from '../BaseSetup';

export const audioNotesSopParamsLabelRegister: ParamsLabelRegister<typeof AudioNotesSopNode, AudioNotesSopNode> = {
	nodeClass: AudioNotesSopNode,
	setupFunc: (node) => ({
		params: [node.p.class, node.p.name, node.p.startOctave, node.p.endOctave],
		callback: () => [
			paramLabelNumber(node.p.class),
			paramLabelString(node.p.name),
			`octaves: ${node.pv.startOctave} -> ${node.pv.endOctave}`,
		],
	}),
};
