import type {GeoObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/Geo';
import {ObjOnCreateHookRegister} from './_Base';
import {ObjType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Obj';

function onCreateHook(node: GeoObjNode) {
	node.createNode('box');
}

export class GeoObjOnCreateRegister extends ObjOnCreateHookRegister<ObjType.GEO> {
	override type(): ObjType.GEO {
		return ObjType.GEO;
	}
	override onCreate(node: GeoObjNode) {
		onCreateHook(node);
	}
}
