import {paramLabelString, paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';
import {MapboxCameraSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/MapboxCamera';

export const mapboxCameraSopParamsLabelRegister: ParamsLabelRegister<typeof MapboxCameraSopNode, MapboxCameraSopNode> =
	{
		nodeClass: MapboxCameraSopNode,
		setupFunc: (node) => ({
			params: node.params.all,
			callback: () => {
				return [
					paramLabelString(node.p.style),
					paramLabelNumber(node.p.longitude),
					paramLabelNumber(node.p.latitude),
					paramLabelNumber(node.p.pitch),
					paramLabelNumber(node.p.bearing),
					paramLabelNumber(node.p.zoom),
				];
			},
		}),
	};
