import type {VaryingWriteGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/VaryingWrite';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {GlOnCreateHookRegister} from './_Base';

export function onCreateHook(node: VaryingWriteGlNode) {
	node.p.name.set(node.name());
}

export class VaryingWriteGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.VARYING_WRITE> {
	override type(): GlType.VARYING_WRITE {
		return GlType.VARYING_WRITE;
	}
	override onCreate(node: VaryingWriteGlNode) {
		onCreateHook(node);
	}
}
