import type {AnimationsNetworkEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/AnimationsNetwork';
import type {AnimationsNetworkMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/AnimationsNetwork';
