type AnimNetworkNode =
	| AnimationsNetworkAnimNode
	| AnimationsNetworkCopNode
	| AnimationsNetworkEventNode
	| AnimationsNetworkMatNode
	| AnimationsNetworkObjNode
	| AnimationsNetworkPostNode
	| AnimationsNetworkRopNode
	| AnimationsNetworkSopNode;

