const CONTEXTS: NodeContext[] = [
	NodeContext.ANIM,
	NodeContext.AUDIO,
	NodeContext.COP,
	NodeContext.EVENT,
	NodeContext.MAT,
	NodeContext.OBJ,
	NodeContext.POST,
	NodeContext.ROP,
	NodeContext.SOP,
];

export const AnimNetworkOnCreateHookRegisters = CONTEXTS.map((context) => {
	class AnimNetworkOnCreateRegister extends BaseOnCreateHookRegister<NodeContext, NetworkNodeType.ANIM> {
		override context(): NodeContext {
			return context;
		}
		override type(): NetworkNodeType.ANIM {
			return NetworkNodeType.ANIM;
		}
		override onCreate(node: any) {
			animNetworkOnCreate(node);
		}
	}
	return AnimNetworkOnCreateRegister;
});
