import {PaletteSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Palette';
import {
	ParamsLabelRegister,
	ParamsLabelResult,
	paramLabelString,
	colorToParamLabelString,
	paramLabelNumber,
} from '../BaseSetup';

export const paletteSopParamsLabelRegister: ParamsLabelRegister<typeof PaletteSopNode, PaletteSopNode> = {
	nodeClass: PaletteSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [paramLabelNumber(node.p.class), paramLabelString(node.p.paletteName)];

			const colorsCount = node.pv.colorsCount;
			const colorValues = [node.pv.color1, node.pv.color2, node.pv.color3, node.pv.color4, node.pv.color5];
			for (let i = 0; i < colorsCount; i++) {
				list.push(colorToParamLabelString(colorValues[i]));
			}

			return list;
		},
	}),
};
