import type {CameraPostProcessSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CameraPostProcess';
import {Vector2, HalfFloatType, NoToneMapping} from 'three';
import {SopOnCreateHookRegister} from './_Base';
import {CameraSopNodeType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/NodeContext';
import {GeoObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/Geo';
import {onCreateHook as CameraRendererOnCreateHook} from './CameraRenderer';

function onCreateHook(node: CameraPostProcessSopNode) {
	const null1 = node.createNode('null');
	const bloom1 = node.createNode('bloom');
	const toneMapping1 = node.createNode('toneMapping');
	toneMapping1.setInput(0, bloom1);
	null1.setInput(0, toneMapping1);
	null1.setName('OUT');
	bloom1.uiData.translate(new Vector2(0, -200));
	null1.uiData.translate(new Vector2(0, 200));
	null1.flags.display.set(true);
	node.p.useOtherNode.set(false);
	node.p.node.set('');

	// we set HalfFloatType so that the effectComposer
	// can output values above 1,
	// which allows the bloom effect to work with material whose value exceeds 1
	node.setTextureType(HalfFloatType);

	// create cameraRenderer
	const parent = node.parent() as GeoObjNode | undefined;
	if (!parent) {
		return;
	}
	const cameraRenderer = parent.createNode('cameraRenderer');
	const {webglRenderer} = CameraRendererOnCreateHook(cameraRenderer);
	webglRenderer.setToneMapping(NoToneMapping);

	node.setInput(0, cameraRenderer);
	const pos = new Vector2();
	pos.copy(node.uiData.position());
	pos.y -= 200;
	cameraRenderer.uiData.setPosition(pos);
}

export class CameraPostProcessSopOnCreateRegister extends SopOnCreateHookRegister<CameraSopNodeType.POST_PROCESS> {
	override type(): CameraSopNodeType.POST_PROCESS {
		return CameraSopNodeType.POST_PROCESS;
	}
	override onCreate(node: CameraPostProcessSopNode) {
		onCreateHook(node);
	}
}
