import {ParamsLabelRegister, ParamsLabelResult, paramLabelNumber} from '../BaseSetup';
import {TubeSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Tube';

export const tubeSopParamsLabelRegister: ParamsLabelRegister<typeof TubeSopNode, TubeSopNode> = {
	nodeClass: TubeSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];
			if (node.pv.singleRadius) {
				list.push(paramLabelNumber(node.p.radius));
			} else {
				list.push(paramLabelNumber(node.p.radiusTop));
				list.push(paramLabelNumber(node.p.radiusBottom));
			}
			list.push(paramLabelNumber(node.p.height));
			return list;
		},
	}),
};
