import {CADBooleanSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CADBoolean';
import {ParamsLabelRegister, paramLabelNumber} from '../BaseSetup';

export const cadBooleanSopParamsLabelRegister: ParamsLabelRegister<typeof CADBooleanSopNode, CADBooleanSopNode> = {
	nodeClass: CADBooleanSopNode,
	setupFunc: (node) => {
		const params = [node.p.operation];
		return {
			params,
			callback: () => {
				return [paramLabelNumber(node.p.operation), paramLabelNumber(node.p.mode)];
			},
		};
	},
};
