import type {ForLoopGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/ForLoop';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {GlOnCreateHookRegister} from './_Base';

export function onCreateHook(node: ForLoopGlNode) {
	node.p.inputsCount.set(1);
	const subnetInput1 = node.createNode('subnetInput');
	const subnetOutput1 = node.createNode('subnetOutput');

	subnetOutput1.setInput(0, subnetInput1, 4);

	subnetInput1.uiData.setPosition(-100, 0);
	subnetOutput1.uiData.setPosition(+100, 0);
}

export class ForLoopGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.FOR_LOOP> {
	override type(): GlType.FOR_LOOP {
		return GlType.FOR_LOOP;
	}
	override onCreate(node: ForLoopGlNode) {
		onCreateHook(node);
	}
}
