import type {ActorInstanceSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/ActorInstance';
import {JsConnectionPointType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';
import {Vector3Param} from '../../../../../../@polygonjs/polygonjs/src/engine/params/Vector3';

function onCreateHook(node: ActorInstanceSopNode) {
	const onTick = node.createNode('onTick');
	const setInstancePosition = node.createNode('setInstancePosition');
	const getInstanceProperty = node.createNode('getInstanceProperty');
	const add1 = node.createNode('add');

	setInstancePosition.setInput(JsConnectionPointType.TRIGGER, onTick);
	setInstancePosition.setInput('position', add1);
	add1.setInput(0, getInstanceProperty, 'instancePosition');
	(add1.params.get('add1')! as Vector3Param).set([0, 0.01, 0]);

	onTick.uiData.setPosition(-100, 0);
	setInstancePosition.uiData.setPosition(400, 0);
	add1.uiData.setPosition(200, 100);
	getInstanceProperty.uiData.setPosition(0, 100);
}

export class ActorInstanceSopOnCreateRegister extends SopOnCreateHookRegister<SopType.ACTOR_INSTANCE> {
	override type(): SopType.ACTOR_INSTANCE {
		return SopType.ACTOR_INSTANCE;
	}
	override onCreate(node: ActorInstanceSopNode) {
		onCreateHook(node);
	}
}
