import {MaterialSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Material';
import {
	ParamsLabelRegister,
	ParamsLabelResult,
	paramLabelString,
	paramLabelNodePath,
	paramLabelBoolean,
} from '../BaseSetup';

export const materialSopParamsLabelRegister: ParamsLabelRegister<typeof MaterialSopNode, MaterialSopNode> = {
	nodeClass: MaterialSopNode,
	setupFunc: (node) => ({
		params: [node.p.material, node.p.group, node.p.cloneMat],
		callback: () => {
			const list: ParamsLabelResult[] = [];

			if (node.pv.group.length > 0) {
				list.push(paramLabelString(node.p.group));
			}
			list.push(paramLabelNodePath(node.p.material));

			if (node.pv.cloneMat) {
				list.push(paramLabelBoolean(node.p.cloneMat));
			}

			return list;
		},
	}),
};
