import type {SpotLightObjNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/obj/SpotLight';
import {ObjOnCreateHookRegister} from './_Base';
import {LightType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Light';

function onCreateHook(node: SpotLightObjNode) {
	node.p.showHelper.set(true);
}

export class SpotLightObjOnCreateRegister extends ObjOnCreateHookRegister<LightType.SPOT> {
	override type(): LightType.SPOT {
		return LightType.SPOT;
	}
	override onCreate(node: SpotLightObjNode) {
		onCreateHook(node);
	}
}
