import {LineSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Line';
import {paramLabelNumber, paramLabelVector3, ParamsLabelRegister} from '../BaseSetup';

export const lineSopParamsLabelRegister: ParamsLabelRegister<typeof LineSopNode, LineSopNode> = {
	nodeClass: LineSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			paramLabelNumber(node.p.length),
			paramLabelNumber(node.p.pointsCount),
			paramLabelVector3(node.p.origin),
			paramLabelVector3(node.p.direction),
		],
	}),
};
