import {NetworkNodeType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/NodeContext';
import type {SubnetGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/Subnet';
import {GlOnCreateHookRegister} from './_Base';

export function onCreateHook(node: SubnetGlNode) {
	node.p.inputsCount.set(1);
	const subnetInput1 = node.createNode('subnetInput');
	const subnetOutput1 = node.createNode('subnetOutput');

	subnetOutput1.setInput(0, subnetInput1);

	subnetInput1.uiData.setPosition(-100, 0);
	subnetOutput1.uiData.setPosition(+100, 0);
}

export class SubnetGlOnCreateHookRegister extends GlOnCreateHookRegister<NetworkNodeType.SUBNET> {
	override type(): NetworkNodeType.SUBNET {
		return NetworkNodeType.SUBNET;
	}
	override onCreate(node: SubnetGlNode) {
		onCreateHook(node);
	}
}
