import {typeClassName, CssType} from '../../../../core/ColorUtils';
import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {MergeSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Merge';
import {ParamsLabelRegister, ParamsLabelResult, COLOR_STRENGTH} from '../BaseSetup';
import {JsConnectionPointType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';

const COMPACT: ParamsLabelResult = {
	detailed: true,
	elements: [
		{
			label: 'compact',
			className: typeClassName(JsConnectionPointType.INT, CssType.TEXT, COLOR_STRENGTH) || 'bg-red-600',
			bold: false,
		},
	],
};

const SEPARATE_OBJECTS: ParamsLabelResult = {
	detailed: true,
	elements: [
		{
			label: 'separate objects',
			className: typeClassName(JsConnectionPointType.COLOR, CssType.TEXT, COLOR_STRENGTH) || 'bg-blue-600',
			bold: false,
		},
	],
};

export const mergeSopParamsLabelRegister: ParamsLabelRegister<typeof MergeSopNode, MergeSopNode> = {
	nodeClass: MergeSopNode,
	setupFunc: (node) => ({
		params: node.p.compact,
		callback: () => (isBooleanTrue(node.pv.compact) ? COMPACT : SEPARATE_OBJECTS),
	}),
};
