import {WFCSolverSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/WFCSolver';
import {ParamsLabelRegister, paramLabelNumber} from '../BaseSetup';

export const WFCSolverSopParamsLabelRegister: ParamsLabelRegister<typeof WFCSolverSopNode, WFCSolverSopNode> = {
	nodeClass: WFCSolverSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			paramLabelNumber(node.p.stepsCount),
			paramLabelNumber(node.p.quadSeed),
			paramLabelNumber(node.p.configSeed),
		],
	}),
};
