import {PointerEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/Pointer';
import {ParamsLabelRegister} from '../BaseSetup';

export const pointerEventParamsLabelRegister: ParamsLabelRegister<typeof PointerEventNode, PointerEventNode> = {
	nodeClass: PointerEventNode,
	setupFunc: (node) => {
		const params = [node.p.pointerdown, node.p.pointermove, node.p.pointerup];
		return {
			params,
			callback: () =>
				params
					.map((p) => {
						return p.value ? p.name() : undefined;
					})
					.filter((v) => v)
					.join(', '),
		};
	},
};
