import type {RampGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/Ramp';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {GlOnCreateHookRegister} from './_Base';

export function onCreateHook(node: RampGlNode) {
	node.p.name.set(node.name());
}

export class RampGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.RAMP> {
	override type(): GlType.RAMP {
		return GlType.RAMP;
	}
	override onCreate(node: RampGlNode) {
		onCreateHook(node);
	}
}
