import { CopType } from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Cop';
import type {BuilderCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/Builder';
import { CopOnCreateHookRegister } from './_Base';

function onCreateHook(node: BuilderCopNode) {
	const globals = node.createNode('globals');
	const output = node.createNode('output');

	globals.uiData.setPosition(-200, 0);
	output.uiData.setPosition(200, 0);
}

export class BuilderCopOnCreateHookRegister extends CopOnCreateHookRegister<CopType.BUILDER> {
	override type(): CopType.BUILDER {
		return CopType.BUILDER
	}
	override onCreate(node: BuilderCopNode) {
		onCreateHook(node);
	}
}
