import type {SubnetSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Subnet';
import {SopOnCreateHookRegister} from './_Base';
import {NetworkNodeType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/NodeContext';

function onCreateHook(node: SubnetSopNode) {
	const subnetInput1 = node.createNode('subnetInput');
	const subnetOutput1 = node.createNode('subnetOutput');

	subnetOutput1.setInput(0, subnetInput1);

	subnetInput1.uiData.setPosition(0, -100);
	subnetOutput1.uiData.setPosition(0, +100);
}

export class SubnetSopOnCreateRegister extends SopOnCreateHookRegister<NetworkNodeType.SUBNET> {
	override type(): NetworkNodeType.SUBNET {
		return NetworkNodeType.SUBNET;
	}
	override onCreate(node: SubnetSopNode) {
		onCreateHook(node);
	}
}
