import {GeometryAttributeCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/GeometryAttribute';
import {ParamsLabelRegister, paramLabelNodePath, paramLabelString} from '../BaseSetup';

export const geometryAttributeCopParamsLabelRegister: ParamsLabelRegister<
	typeof GeometryAttributeCopNode,
	GeometryAttributeCopNode
> = {
	nodeClass: GeometryAttributeCopNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: () => {
				return [
					paramLabelNodePath(node.p.node),
					paramLabelString(node.p.group),
					paramLabelString(node.p.attribute),
				];
			},
		};
	},
};
