import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';
import {IcosahedronSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Icosahedron';

export const icosahedronSopParamsLabelRegister: ParamsLabelRegister<typeof IcosahedronSopNode, IcosahedronSopNode> = {
	nodeClass: IcosahedronSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list = [
				`radius: ${node.pv.radius}`,
				`detail: ${node.pv.detail}`,
				`${booleanAndIcon(node.pv.pointsOnly)} pointsOnly`,
			];

			return list;
		},
	}),
};
