import {ParamsLabelRegister} from '../BaseSetup';
import {BuilderCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/Builder';

export const builderCopParamsLabelRegister: ParamsLabelRegister<typeof BuilderCopNode, BuilderCopNode> = {
	nodeClass: BuilderCopNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: () => [
				`${node.pv.resolution.x} x ${node.pv.resolution.y} (= ${node.pv.resolution.x * node.pv.resolution.y})`,
				...node.textureParamsController.paramLabels(),
			],
		};
	},
};
