import {EnvMapMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/EnvMap';
import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';

export const envMapMatParamsLabelRegister: ParamsLabelRegister<typeof EnvMapMatNode, EnvMapMatNode> = {
	nodeClass: EnvMapMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			`${booleanAndIcon(node.pv.useEnvMap)} useEnvMap` + (node.pv.useEnvMap ? `: ${node.pv.envMap.path()}` : ``),
		],
	}),
};
