import type {TextureGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/Texture';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {GlOnCreateHookRegister} from './_Base';

export function onCreateHook(node: TextureGlNode) {
	node.p.paramName.set(node.name());
}

export class TextureGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.TEXTURE> {
	override type(): GlType.TEXTURE {
		return GlType.TEXTURE;
	}
	override onCreate(node: TextureGlNode) {
		onCreateHook(node);
	}
}
