import {paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';
import {TetrahedralizeSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Tetrahedralize';

export const tetrahedralizeSopParamsLabelRegister: ParamsLabelRegister<
	typeof TetrahedralizeSopNode,
	TetrahedralizeSopNode
> = {
	nodeClass: TetrahedralizeSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list = [paramLabelNumber(node.p.innerPointsResolution), paramLabelNumber(node.p.minQuality)];
			if (node.pv.stepByStep) {
				list.push(paramLabelNumber(node.p.step));
			}

			return list;
		},
	}),
};
