import type {CameraControlsSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CameraControls';
import {
	CameraControlsNodeType,
	CameraSopNodeType,
} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/NodeContext';
import {SopOnCreateHookRegister} from './_Base';

function onCreateHook(node: CameraControlsSopNode) {
	const controls = node.createNode(CameraControlsNodeType.ORBIT);
	node.p.node.setNode(controls, {relative: true});
}

export class CameraControlsSopOnCreateRegister extends SopOnCreateHookRegister<CameraSopNodeType.CONTROLS> {
	override type(): CameraSopNodeType.CONTROLS {
		return CameraSopNodeType.CONTROLS;
	}
	override onCreate(node: CameraControlsSopNode) {
		onCreateHook(node);
	}
}
