import {ObjectsLayoutSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/ObjectsLayout';
import {paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';

export const objectsLayoutSopParamsLabelRegister: ParamsLabelRegister<
	typeof ObjectsLayoutSopNode,
	ObjectsLayoutSopNode
> = {
	nodeClass: ObjectsLayoutSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [paramLabelNumber(node.p.maxLayoutWidth), paramLabelNumber(node.p.rowHeight)];
		},
	}),
};
