import {BaseParamType} from '../../../../../../@polygonjs/polygonjs/src/engine/params/_Base';
import {VideoCopNode, VideoMode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/Video';
import type {StringParam} from '../../../../../../@polygonjs/polygonjs/src/engine/params/String';
import {ParamsLabelRegister, ParamsLabelResult, paramLabelString, paramLabelNumber} from '../BaseSetup';

export const videoCopParamsLabelRegister: ParamsLabelRegister<typeof VideoCopNode, VideoCopNode> = {
	nodeClass: VideoCopNode,
	setupFunc: (node) => {
		let params: BaseParamType[] = [node.p.mode, node.p.urlsCount, node.p.url1, node.p.url2, node.p.url3];
		params = params.concat(node.textureParamsController.paramLabelsParams());

		return {
			params,
			callback: () => {
				const list: ParamsLabelResult[] = [paramLabelNumber(node.p.mode)];
				const mode = node.mode();
				switch (mode) {
					case VideoMode.FROM_URLS: {
						list.push(...node.urlParams().map((p: StringParam) => paramLabelString(p)));
						break;
					}
					case VideoMode.FROM_HTML_ELEMENT: {
						list.push(paramLabelString(node.p.selector));
						break;
					}
				}

				const textureLabels = node.textureParamsController.paramLabels();
				list.push(...textureLabels);
				return list;

				// const expression = node.p.url.expressionController?.expression();
				// if (expression) {
				// 	const detailed: ParamLabelResultDetailed = {
				// 		detailed: true,
				// 		elements: [
				// 			{
				// 				label: expression,
				// 				className: EXPRESSION_CLASS,
				// 				bold: true,
				// 			},
				// 		],
				// 	};
				// 	return detailed;
				// }
				// const url = node.p.url.rawInput();
				// if (url) {
				// 	const elements = url.split('/');
				// 	const urlLabel = elements[elements.length - 1];
				//
				// 	return [urlLabel].concat(textureLabels);
				// } else {
				// 	return '';
				// }
			},
		};
	},
};
