import {paramLabelNumber, ParamsLabelRegister} from '../BaseSetup';
import {OrthographicCameraSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/OrthographicCamera';

export const orthographicCameraSopParamsLabelRegister: ParamsLabelRegister<
	typeof OrthographicCameraSopNode,
	OrthographicCameraSopNode
> = {
	nodeClass: OrthographicCameraSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [paramLabelNumber(node.p.size), paramLabelNumber(node.p.near), paramLabelNumber(node.p.far)];
		},
	}),
};
