import {ParamsLabelRegister} from '../BaseSetup';
import {ConeSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Cone';

export const coneSopParamsLabelRegister: ParamsLabelRegister<typeof ConeSopNode, ConeSopNode> = {
	nodeClass: ConeSopNode,
	setupFunc: (node) => ({
		params: [node.p.radius, node.p.height],
		callback: () => {
			return [`radius: ${node.pv.radius}`, `height: ${node.pv.height}`];
		},
	}),
};
