import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {ScatterSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Scatter';
import {
	ParamsLabelResult,
	ParamsLabelRegister,
	paramLabelNumber,
	paramLabelBoolean,
	paramLabelString,
} from '../BaseSetup';

export const scatterSopParamsLabelRegister: ParamsLabelRegister<typeof ScatterSopNode, ScatterSopNode> = {
	nodeClass: ScatterSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [
				paramLabelNumber(node.p.pointsCount),
				paramLabelNumber(node.p.seed),
				paramLabelBoolean(node.p.useWeightAttribute),
			];
			if (isBooleanTrue(node.pv.useWeightAttribute)) {
				list.push(paramLabelString(node.p.weightAttribute));
			}
			list.push(paramLabelBoolean(node.p.transferAttributes));
			if (isBooleanTrue(node.pv.transferAttributes)) {
				list.push(paramLabelString(node.p.attributesToTransfer));
			}
			if (isBooleanTrue(node.pv.addIdAttribute)) {
				list.push(paramLabelBoolean(node.p.addIdAttribute));
			}
			if (isBooleanTrue(node.pv.addIdnAttribute)) {
				list.push(paramLabelBoolean(node.p.addIdnAttribute));
			}
			return list;
		},
	}),
};
