import type {RayMarchingBuilderMatNode} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/RayMarchingBuilder';
import type {TextureSDFGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/TextureSDF';
import {GlConnectionPointType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Gl';
import {GlType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Gl';
import {GlOnCreateHookRegister} from './_Base';

export function onCreateHook(node: TextureSDFGlNode) {
	const textureUniformName = node.name();
	// const boundMinExpression = `\`ch('../${node.name()}/paramName')\`boundMin`;
	// const boundMaxExpression = `\`ch('../${node.name()}/paramName')\`boundMax`;
	const boundMinParamName = `${node.name()}BoundMin`;
	const boundMaxParamName = `${node.name()}BoundMax`;
	node.p.paramName.set(textureUniformName);

	const parent = node.parent() as RayMarchingBuilderMatNode | undefined;
	if (!parent) {
		return;
	}
	// const boundMinParamName = 'boundMinParam';
	// const boundMaxParamName = 'boundMaxParam';
	const _createParam = (nodeName: string) => {
		const paramNode = parent.createNode('param');
		paramNode.setName(nodeName);
		return paramNode;
	};
	const boundMinParam =
		parent.nodesByType('param').find((n) => n.name() == boundMinParamName) || _createParam(boundMinParamName);
	const boundMaxParam =
		parent.nodesByType('param').find((n) => n.name() == boundMaxParamName) || _createParam(boundMaxParamName);

	[boundMinParam, boundMaxParam].forEach((paramNode) => {
		paramNode.setGlType(GlConnectionPointType.VEC3);
	});
	boundMinParam.p.name.set(boundMinParamName);
	boundMaxParam.p.name.set(boundMaxParamName);

	node.setInput('boundMin', boundMinParam);
	node.setInput('boundMax', boundMaxParam);

	const texturePos = node.uiData.position();
	boundMinParam.uiData.setPosition(texturePos.x - 300, texturePos.y - 0);
	boundMaxParam.uiData.setPosition(texturePos.x - 300, texturePos.y + 200);
}

export class TextureSDFGlOnCreateHookRegister extends GlOnCreateHookRegister<GlType.TEXTURE_SDF> {
	override type(): GlType.TEXTURE_SDF {
		return GlType.TEXTURE_SDF;
	}
	override onCreate(node: TextureSDFGlNode) {
		onCreateHook(node);
	}
}
