import {MeshPhysicalBuilderMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshPhysicalBuilder';
import {booleanAndIcon, ParamsLabelRegister} from '../BaseSetup';

export const meshPhysicalBuilderMatParamsLabelRegister: ParamsLabelRegister<
	typeof MeshPhysicalBuilderMatNode,
	MeshPhysicalBuilderMatNode
> = {
	nodeClass: MeshPhysicalBuilderMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			`${booleanAndIcon(node.pv.useEnvMap)} useEnvMap` + (node.pv.useEnvMap ? `: ${node.pv.envMap.path()}` : ``),
			`roughness: ${node.pv.roughness}`,
			`metalness: ${node.pv.metalness}`,
		],
	}),
};
