import {PhysicsGroundSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PhysicsGround';
import {ParamsLabelRegister, paramLabelVector3, paramLabelNumber} from '../BaseSetup';

export const physicsGroundSopParamsLabelRegister: ParamsLabelRegister<
	typeof PhysicsGroundSopNode,
	PhysicsGroundSopNode
> = {
	nodeClass: PhysicsGroundSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			return [
				paramLabelVector3(node.p.direction),
				paramLabelNumber(node.p.height),
				paramLabelNumber(node.p.thickness),
				paramLabelNumber(node.p.friction),
				paramLabelNumber(node.p.restitution),
			];
		},
	}),
};
