import {BaseParamType} from '../../../../../../@polygonjs/polygonjs/src/engine/params/_Base';
import {ParamsLabelRegister, paramLabelNumber} from '../BaseSetup';
import {WebCamCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/WebCam';

function getParams(node: WebCamCopNode) {
	let params: BaseParamType[] = [node.p.facingMode];
	params = params.concat(node.textureParamsController.paramLabelsParams());
	return params;
}
function getCallback(node: WebCamCopNode) {
	const facingModeLabel = paramLabelNumber(node.p.facingMode);
	const textureLabels = node.textureParamsController.paramLabels();
	return [facingModeLabel].concat(textureLabels);
}

export const webCamCopParamsLabelRegister: ParamsLabelRegister<typeof WebCamCopNode, WebCamCopNode> = {
	nodeClass: WebCamCopNode,
	setupFunc: (node) => {
		return {
			params: getParams(node),
			callback: () => getCallback(node),
		};
	},
};
