import {ParamsLabelRegister} from '../BaseSetup';
import {BoxLinesSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/BoxLines';

export const boxLinesSopParamsLabelRegister: ParamsLabelRegister<typeof BoxLinesSopNode, BoxLinesSopNode> = {
	nodeClass: BoxLinesSopNode,
	setupFunc: (node) => ({
		params: [node.p.sizes, node.p.size],
		callback: () => {
			return [`sizes: ${node.pv.sizes.toArray().join(',')}`, `size: ${node.pv.size}`];
		},
	}),
};
