import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {SDFRepeatGlNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/gl/SDFRepeat';
import {ParamsLabelRegister} from '../BaseSetup';

const x = 'X';
const y = 'Y';
const z = 'Z';
export const SDFRepeatGlParamsLabelRegister: ParamsLabelRegister<typeof SDFRepeatGlNode, SDFRepeatGlNode> = {
	nodeClass: SDFRepeatGlNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			let axises: string[] = [];
			if (node.repeatAll()) {
				axises.push(x, y, z);
			} else {
				if (isBooleanTrue(node.pv.repeatX)) {
					axises.push(x);
				}
				if (isBooleanTrue(node.pv.repeatY)) {
					axises.push(y);
				}
				if (isBooleanTrue(node.pv.repeatZ)) {
					axises.push(z);
				}
			}

			const list = [`repeat: ${axises.join(',')}`];

			if (node.clamped()) {
				let clampedAxises: string[] = [];
				if (node.clampedAll()) {
					clampedAxises.push(x, y, z);
				} else {
					if (node.clampedX()) {
						clampedAxises.push(x);
					}
					if (node.clampedY()) {
						clampedAxises.push(y);
					}
					if (node.clampedZ()) {
						clampedAxises.push(z);
					}
				}
				list.push(`clamped: ${clampedAxises.join(',')}`);
			}

			return list;
		},
	}),
};
