import {TypeAssert} from './../../../../../../@polygonjs/polygonjs/src/engine/poly/Assert';
import {PhysicsRBDJointsSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PhysicsRBDJoints';
import {PhysicsJointType} from './../../../../../../@polygonjs/polygonjs/src/core/physics/PhysicsJoint';
import {
	ParamsLabelRegister,
	paramLabelNumber,
	paramLabelString,
	ParamsLabelResult,
	paramLabelVector2,
	paramLabelBoolean,
} from '../BaseSetup';

export const physicsRBDJointSopParamsLabelRegister: ParamsLabelRegister<
	typeof PhysicsRBDJointsSopNode,
	PhysicsRBDJointsSopNode
> = {
	nodeClass: PhysicsRBDJointsSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const jointType = node.jointType();
			const list: ParamsLabelResult[] = [
				// paramLabelNumber(node.p.colliderType),
				// paramLabelNumber(node.p.sizeMethod),
			];
			if (node.pv.group != '') {
				list.push(paramLabelString(node.p.group));
			}
			list.push(paramLabelNumber(node.p.maxDistance));
			list.push(paramLabelNumber(node.p.jointType));

			// if (node.sizeMethod() == SizeComputationMethod.CUSTOM) {
			list.push(...joinTypeDetails(jointType, node));
			// }
			list.push(paramLabelBoolean(node.p.anchorsAtCenter));

			list.push(
				...[
					// paramLabelNumber(node.p.density),
					// paramLabelNumber(node.p.friction),
					// paramLabelNumber(node.p.restitution),
					// paramLabelNumber(node.p.linearDamping),
					paramLabelBoolean(node.p.ouputsJointsOnly),
				]
			);

			return list;
		},
	}),
};

function joinTypeDetails(jointType: PhysicsJointType, node: PhysicsRBDJointsSopNode): ParamsLabelResult[] {
	switch (jointType) {
		case PhysicsJointType.FIXED: {
			return [];
		}
		case PhysicsJointType.PRISMATIC: {
			return [paramLabelNumber(node.p.center), paramLabelVector2(node.p.limit)];
		}
		case PhysicsJointType.REVOLUT: {
			return [paramLabelNumber(node.p.center), paramLabelVector2(node.p.limit)];
		}
		case PhysicsJointType.SPHERICAL: {
			return [paramLabelNumber(node.p.center)];
		}
	}

	TypeAssert.unreachable(jointType);
}
