import {TypeAssert} from './../../../../../../@polygonjs/polygonjs/src/engine/poly/Assert';
import {PhysicsRBDColliderType} from './../../../../../../@polygonjs/polygonjs/src/core/physics/PhysicsAttribute';
import {
	PhysicsRBDAttributesSopNode,
	BORDER_RADIUS_AVAILABLE,
} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PhysicsRBDAttributes';
import {SizeComputationMethod} from '../../../../../../@polygonjs/polygonjs/src/engine/operations/sop/PhysicsRBDAttributes';
import {
	ParamsLabelRegister,
	paramLabelNumber,
	ParamsLabelResult,
	paramLabelVector3,
	paramLabelBoolean,
} from '../BaseSetup';

export const physicsRBDAttributesSopParamsLabelRegister: ParamsLabelRegister<
	typeof PhysicsRBDAttributesSopNode,
	PhysicsRBDAttributesSopNode
> = {
	nodeClass: PhysicsRBDAttributesSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const colliderType = node.colliderType();
			const list: ParamsLabelResult[] = [
				paramLabelNumber(node.p.RBDType),
				paramLabelNumber(node.p.colliderType),
				paramLabelNumber(node.p.sizeMethod),
			];

			if (node.sizeMethod() == SizeComputationMethod.MANUAL) {
				list.push(...colliderTypeDetails(colliderType, node));
			}
			if (BORDER_RADIUS_AVAILABLE.includes(node.colliderType())) {
				list.push(paramLabelNumber(node.p.borderRadius));
			}

			list.push(
				...[
					paramLabelNumber(node.p.density),
					paramLabelNumber(node.p.friction),
					paramLabelNumber(node.p.restitution),
					paramLabelNumber(node.p.linearDamping),
					paramLabelNumber(node.p.angularDamping),
					paramLabelBoolean(node.p.canSleep),
				]
			);

			return list;
		},
	}),
};

function colliderTypeDetails(
	colliderType: PhysicsRBDColliderType,
	node: PhysicsRBDAttributesSopNode
): ParamsLabelResult[] {
	switch (colliderType) {
		case PhysicsRBDColliderType.CAPSULE: {
			return [paramLabelNumber(node.p.radius), paramLabelNumber(node.p.height)];
		}
		case PhysicsRBDColliderType.CONE: {
			return [paramLabelNumber(node.p.radius), paramLabelNumber(node.p.height)];
		}
		case PhysicsRBDColliderType.CYLINDER: {
			return [paramLabelNumber(node.p.radius), paramLabelNumber(node.p.height)];
		}
		case PhysicsRBDColliderType.CUBOID: {
			return [paramLabelVector3(node.p.sizes), paramLabelNumber(node.p.size)];
		}
		case PhysicsRBDColliderType.SPHERE: {
			return [paramLabelNumber(node.p.radius)];
		}
		case PhysicsRBDColliderType.HEIGHT_FIELD: {
			return [paramLabelNumber(node.p.rows), paramLabelNumber(node.p.cols)];
		}
		case PhysicsRBDColliderType.CONVEX_HULL:
		case PhysicsRBDColliderType.TRIMESH: {
			return [];
		}
	}

	TypeAssert.unreachable(colliderType);
}
