import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {ArrayUtils} from '../../../../../../@polygonjs/polygonjs/src/core/ArrayUtils';
import {RestAttributesSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/RestAttributes';
import {ParamsLabelRegister} from '../BaseSetup';

const compactElements: string[] = [];
export const restAttributesSopParamsLabelRegister: ParamsLabelRegister<
	typeof RestAttributesSopNode,
	RestAttributesSopNode
> = {
	nodeClass: RestAttributesSopNode,
	setupFunc: (node) => ({
		params: [node.p.tposition, node.p.position, node.p.restP, node.p.tnormal, node.p.normal, node.p.restN],
		callback: () => {
			ArrayUtils.compact(
				[
					isBooleanTrue(node.pv.tposition) ? `${node.pv.position} -> ${node.pv.restP}` : null,
					isBooleanTrue(node.pv.tnormal) ? `${node.pv.normal} -> ${node.pv.restN}` : null,
				],
				compactElements
			);
			return compactElements.join(', ');
		},
	}),
};
