import type {SpotLightSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/SpotLight';
import {SopOnCreateHookRegister} from './_Base';
import {LightType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Light';

function onCreateHook(node: SpotLightSopNode) {
	node.p.showHelper.set(true);
}

export class SpotLightSopOnCreateRegister extends SopOnCreateHookRegister<LightType.SPOT> {
	override type(): LightType.SPOT {
		return LightType.SPOT;
	}
	override onCreate(node: SpotLightSopNode) {
		onCreateHook(node);
	}
}
