import {TouchEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/Touch';
import {ParamsLabelRegister} from '../BaseSetup';

export const touchEventParamsLabelRegister: ParamsLabelRegister<typeof TouchEventNode, TouchEventNode> = {
	nodeClass: TouchEventNode,
	setupFunc: (node) => {
		const params = [node.p.touchstart, node.p.touchmove, node.p.touchend];
		return {
			params,
			callback: () =>
				params
					.map((p) => {
						return p.value ? p.name() : undefined;
					})
					.filter((v) => v)
					.join(', '),
		};
	},
};
