import {MapboxElevationCopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/cop/MapboxElevation';
import {ParamsLabelRegister, paramLabelNumber, paramLabelBoolean} from '../BaseSetup';

export const mapboxElevationCopParamsLabelRegister: ParamsLabelRegister<
	typeof MapboxElevationCopNode,
	MapboxElevationCopNode
> = {
	nodeClass: MapboxElevationCopNode,
	setupFunc: (node) => {
		return {
			params: node.params.all,
			callback: () => {
				return [
					paramLabelNumber(node.p.longitude),
					paramLabelNumber(node.p.latitude),
					paramLabelNumber(node.p.zoom),
					paramLabelBoolean(node.p.highres),
				];
			},
		};
	},
};
