import {MeshMatcapMatNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/mat/MeshMatcap';
import {booleanAndIcon, colorToParamLabelString, ParamsLabelRegister} from '../BaseSetup';

export const meshMatcapMatParamsLabelRegister: ParamsLabelRegister<typeof MeshMatcapMatNode, MeshMatcapMatNode> = {
	nodeClass: MeshMatcapMatNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => [
			colorToParamLabelString(node.pv.color),
			`${booleanAndIcon(node.pv.useMatcapMap)} matcap` +
				(node.pv.useMatcapMap ? `: ${node.pv.matcapMap.path()}` : ``),
		],
	}),
};
