import {TypeAssert} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/Assert';
import {
	AnimationPositionMode,
	ANIMATION_POSITION_RELATIVE_TOS,
} from '../../../../../../@polygonjs/polygonjs/src/core/animation/Position';
import {ANIMATION_POSITION_MODES} from '../../../../../../@polygonjs/polygonjs/src/core/animation/Position';
import {PositionAnimNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/anim/Position';
import {ParamsLabelRegister} from '../BaseSetup';

export const positionAnimParamsLabelRegister: ParamsLabelRegister<typeof PositionAnimNode, PositionAnimNode> = {
	nodeClass: PositionAnimNode,
	setupFunc: (node) => ({
		params: [node.p.mode, node.p.relativeTo, node.p.offset],
		callback: () => {
			const mode = ANIMATION_POSITION_MODES[node.pv.mode];
			switch (mode) {
				case AnimationPositionMode.RELATIVE:
					return _relativeLabel(node);
				case AnimationPositionMode.ABSOLUTE:
					return _absoluteLabel();
			}
			TypeAssert.unreachable(mode);
		},
	}),
};

function _relativeLabel(node: PositionAnimNode) {
	const after_before = node.pv.offset > 0 ? 'after' : 'before';
	const relative_to = ANIMATION_POSITION_RELATIVE_TOS[node.pv.relativeTo];
	return `${Math.abs(node.pv.offset)} ${after_before} ${relative_to}`;
}
function _absoluteLabel() {
	return 'absolute';
}
