import type {TexturePostNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/post/Texture';
import {CopNetworkPostNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/post/CopNetwork';
import {PostProcessNetworkSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/PostProcessNetwork';
import {ASSETS_ROOT} from '../../../../../../@polygonjs/polygonjs/src/core/loader/AssetsUtils';
import {PostOnCreateHookRegister} from './_Base';
import {PostType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Post';

const COP_NAME = 'COP';
const IMAGE_COP_NAME = 'image_POST_TEXTURE';

function onCreateHook(node: TexturePostNode) {
	const parentNode = node.parent() as PostProcessNetworkSopNode;

	function createCopNetwork(parentNode: PostProcessNetworkSopNode) {
		const COP = parentNode.createNode('copNetwork');
		COP.setName(COP_NAME);

		const nodePos = node.uiData.position();
		COP.uiData.setPosition(nodePos.x - 200, nodePos.y);

		return COP;
	}

	const COP = parentNode.nodesByType('copNetwork')[0] || createCopNetwork(parentNode);

	function createImage(COP: CopNetworkPostNode) {
		const imageNode = COP.children()[0] || COP.createNode('image');
		imageNode.p.url.set(`${ASSETS_ROOT}/textures/scratches.jpg`);

		return imageNode;
	}
	const imageNode = COP.node(IMAGE_COP_NAME) || createImage(COP);

	node.p.texture.setNode(imageNode, {relative: true});
}

export class TexturePostOnCreateRegister extends PostOnCreateHookRegister<PostType.TEXTURE> {
	override type(): PostType.TEXTURE {
		return PostType.TEXTURE;
	}
	override onCreate(node: TexturePostNode) {
		onCreateHook(node);
	}
}
