import {Vector2Param} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Vector2';
import {Vector3Param} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Vector3';
import {Vector4Param} from './../../../../../../@polygonjs/polygonjs/src/engine/params/Vector4';
import {ArrayUtils} from './../../../../../../@polygonjs/polygonjs/src/core/ArrayUtils';
import {
	EXPRESSION_CLASS,
	ParamLabelOptions,
	ParamsLabelResult,
	NO_EXPRESSION_CLASS,
	ParamLabelResultColoredText,
} from './_Base';

export function _paramLabelVector(
	param: Vector2Param | Vector3Param | Vector4Param,
	options?: ParamLabelOptions
): ParamsLabelResult {
	const label = options?.displayLabel == false ? undefined : `${param.name()}:`;

	if (param.hasExpression()) {
		const compactElements: Array<string | ParamLabelResultColoredText> = [];
		ArrayUtils.compact(
			[
				label,
				...param.components.map((c) => ({
					label: c.rawInput(),
					className: c.hasExpression() ? EXPRESSION_CLASS : NO_EXPRESSION_CLASS,
					bold: true,
				})),
			],
			compactElements
		);
		return {
			detailed: true,
			elements: compactElements,
		};
	} else {
		const compactElements: Array<string | number> = [];
		ArrayUtils.compact([label, ...param.components.map((c) => c.rawInput())], compactElements);
		return compactElements.join(' ');
	}
}
