import {ParamsLabelResult, ParamsLabelRegister, paramLabelNumber, paramLabelBoolean} from '../BaseSetup';
import {CameraWebXRVRSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CameraWebXRVR';
import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';

export const cameraWebXRVRSopParamsLabelRegister: ParamsLabelRegister<
	typeof CameraWebXRVRSopNode,
	CameraWebXRVRSopNode
> = {
	nodeClass: CameraWebXRVRSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];

			list.push(paramLabelNumber(node.p.localFloor));
			list.push(paramLabelNumber(node.p.boundedFloor));
			list.push(paramLabelNumber(node.p.handTracking));
			list.push(paramLabelNumber(node.p.layers));

			list.push(paramLabelBoolean(node.p.overrideReferenceSpaceType));
			if (isBooleanTrue(node.pv.overrideReferenceSpaceType)) {
				list.push(paramLabelNumber(node.p.referenceSpaceType));
			}

			return list;
		},
	}),
};
