import {ParamsLabelResult} from './../BaseSetup';
import {ActorSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/Actor';
import {ParamsLabelRegister, paramLabelString, paramLabelNodePath} from '../BaseSetup';

export const actorSopParamsLabelRegister: ParamsLabelRegister<typeof ActorSopNode, ActorSopNode> = {
	nodeClass: ActorSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [];

			const {group, useThisNode} = node.pv;
			if (group.trim() != '') {
				list.push(paramLabelString(node.p.group));
			}

			if (!useThisNode) {
				list.push(paramLabelNodePath(node.p.node));
			}

			return list;
		},
	}),
};
