import {WFCTileSideNameSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/WFCTileSideName';
import {ParamsLabelRegister, paramLabelBoolean, ParamsLabelResult} from '../BaseSetup';

export const WFCTileSideNameSopParamsLabelRegister: ParamsLabelRegister<
	typeof WFCTileSideNameSopNode,
	WFCTileSideNameSopNode
> = {
	nodeClass: WFCTileSideNameSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const {sameNameForSelectedSides, south, north, west, east, bottom, top} = node.pv;
			const sideNames: string[] = [];
			if (sameNameForSelectedSides == true) {
				sideNames.push(node.pv.sideName);
			} else {
				if (south) sideNames.push(node.pv.southName);
				if (north) sideNames.push(node.pv.northName);
				if (west) sideNames.push(node.pv.westName);
				if (east) sideNames.push(node.pv.eastName);
				if (bottom) sideNames.push(node.pv.bottomName);
				if (top) sideNames.push(node.pv.topName);
			}

			const results: ParamsLabelResult[] = [];

			for (const sideName of sideNames) {
				const sides: string[] = [];
				if (south && (sameNameForSelectedSides || node.pv.southName == sideName)) sides.push('s');
				if (north && (sameNameForSelectedSides || node.pv.northName == sideName)) sides.push('n');
				if (west && (sameNameForSelectedSides || node.pv.westName == sideName)) sides.push('w');
				if (east && (sameNameForSelectedSides || node.pv.eastName == sideName)) sides.push('e');
				if (bottom && (sameNameForSelectedSides || node.pv.bottomName == sideName)) sides.push('b');
				if (top && (sameNameForSelectedSides || node.pv.topName == sideName)) sides.push('t');
				results.push(`${sideName}: ${sides.join(', ')}`);
			}

			if (node.pv.highlight) {
				results.push(paramLabelBoolean(node.p.highlight));
			}

			return results;
		},
	}),
};
