import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {RepeatAnimNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/anim/Repeat';
import {ParamsLabelRegister} from '../BaseSetup';

export const repeatAnimParamsLabelRegister: ParamsLabelRegister<typeof RepeatAnimNode, RepeatAnimNode> = {
	nodeClass: RepeatAnimNode,
	setupFunc: (node) => ({
		params: [node.p.unlimited, node.p.count, node.p.yoyo, node.p.delay],
		callback: () => {
			const count = `${isBooleanTrue(node.pv.unlimited) ? '' : `${node.pv.count}x`}`;
			const usedProps = {
				yoyo: isBooleanTrue(node.pv.yoyo),
				delay: node.pv.delay != 0,
			};
			const hasUsedProps = usedProps.yoyo || usedProps.delay;
			if (hasUsedProps) {
				const props: string[] = [];
				if (usedProps.yoyo) {
					props.push(`yoyo: ${node.pv.yoyo}`);
				}
				if (usedProps.delay) {
					props.push(`delay: ${node.pv.delay}`);
				}
				return `${count} (${props.join(', ')})`;
			} else {
				return `${count}`;
			}
		},
	}),
};
