import {CADGroupSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CADGroup';
import {
	paramLabelString,
	paramLabelBoolean,
	paramLabelNumber,
	ParamsLabelRegister,
	ParamsLabelResult,
} from '../BaseSetup';
import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';

export const cadGroupSopParamsLabelRegister: ParamsLabelRegister<typeof CADGroupSopNode, CADGroupSopNode> = {
	nodeClass: CADGroupSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [paramLabelString(node.p.name), paramLabelNumber(node.p.operation)];

			if (isBooleanTrue(node.pv.byExpression)) {
				list.push(paramLabelBoolean(node.p.byExpression));
			}
			if (isBooleanTrue(node.pv.byBoundingBox)) {
				list.push(paramLabelBoolean(node.p.byBoundingBox));
			}
			if (isBooleanTrue(node.pv.byBoundingObject)) {
				list.push(paramLabelBoolean(node.p.byBoundingObject));
			}

			return list;
		},
	}),
};
