import {WindowEventNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/event/Window';
import {ParamsLabelRegister} from '../BaseSetup';

export const windowEventParamsLabelRegister: ParamsLabelRegister<typeof WindowEventNode, WindowEventNode> = {
	nodeClass: WindowEventNode,
	setupFunc: (node) => {
		const params = [node.p.resize];
		return {
			params,
			callback: () =>
				params
					.map((p) => {
						return p.value ? p.name() : undefined;
					})
					.filter((v) => v)
					.join(', '),
		};
	},
};
