import type {DirectionalLightSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/DirectionalLight';
import {SopOnCreateHookRegister} from './_Base';
import {LightType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Light';

function onCreateHook(node: DirectionalLightSopNode) {
	node.p.showHelper.set(true);
}

export class DirectionalLightSopOnCreateRegister extends SopOnCreateHookRegister<LightType.DIRECTIONAL> {
	override type(): LightType.DIRECTIONAL {
		return LightType.DIRECTIONAL;
	}
	override onCreate(node: DirectionalLightSopNode) {
		onCreateHook(node);
	}
}
