import {AttribAddMultSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/AttribAddMult';
import {ParamsLabelRegister} from '../BaseSetup';

export const attribAddMultSopParamsLabelRegister: ParamsLabelRegister<
	typeof AttribAddMultSopNode,
	AttribAddMultSopNode
> = {
	nodeClass: AttribAddMultSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			function _sign(value: number) {
				return value < 0 ? '-' : '+';
			}

			const arg0 = node.pv.preAdd == 0 ? 'value' : `(value ${_sign(node.pv.preAdd)} ${Math.abs(node.pv.preAdd)})`;
			const arg1 = node.pv.mult == 1 ? arg0 : `${node.pv.mult} * ${arg0}`;
			const arg2 =
				node.pv.postAdd == 0 ? arg1 : `= (${arg1}) ${_sign(node.pv.postAdd)} ${Math.abs(node.pv.postAdd)}`;

			return [node.pv.name, arg2];
		},
	}),
};
