import {CSGBooleanSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CSGBoolean';
import {ParamsLabelRegister, paramLabelNumber} from '../BaseSetup';

export const csgBooleanSopParamsLabelRegister: ParamsLabelRegister<typeof CSGBooleanSopNode, CSGBooleanSopNode> = {
	nodeClass: CSGBooleanSopNode,
	setupFunc: (node) => {
		const params = [node.p.operation];
		return {
			params,
			callback: () => {
				return [paramLabelNumber(node.p.operation)];
			},
		};
	},
};
