import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {AttributeJsNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/Attribute';
import {ParamsLabelRegister} from '../BaseSetup';

export const attributeJsParamsLabelRegister: ParamsLabelRegister<typeof AttributeJsNode, AttributeJsNode> = {
	nodeClass: AttributeJsNode,
	setupFunc: (node) => ({
		params: [node.p.name, node.p.exportWhenConnected],
		callback: () => (isBooleanTrue(node.pv.exportWhenConnected) ? `${node.pv.name} (EXPORTED)` : node.pv.name),
	}),
};
