import {isBooleanTrue} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {AttribRemapSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/AttribRemap';
import {ParamsLabelRegister} from '../BaseSetup';

export const attribRemapSopParamsLabelRegister: ParamsLabelRegister<typeof AttribRemapSopNode, AttribRemapSopNode> = {
	nodeClass: AttribRemapSopNode,
	setupFunc: (node) => ({
		params: [node.p.name, node.p.changeName, node.p.newName],
		callback: () => {
			if (isBooleanTrue(node.pv.changeName)) {
				return `${node.pv.name} - ${node.pv.newName}`;
			} else {
				return node.pv.name;
			}
		},
	}),
};
