import {EntityVariable} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/js/code/assemblers/entityBuilder/EntityBuilderAssemblerCommon';
import type {EntityBuilderSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/EntityBuilder';
import {SopOnCreateHookRegister} from './_Base';
import {SopType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/registers/nodes/types/Sop';
import {JsConnectionPointType} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/io/connections/Js';

function onCreateHook(node: EntityBuilderSopNode) {
	const globals = node.createNode('globals');
	const attribute1 = node.createNode('attribute');
	// const add1 = node.createNode('add');

	// add1.setInput(0, globals, EntityVariable.POSITION);
	attribute1.setInput(0, globals, EntityVariable.INDEX);
	attribute1.setJsType(JsConnectionPointType.INT);
	attribute1.p.name.set('index');
	attribute1.p.exportWhenConnected.set(1);

	// add1.p.radius.set(0.4);

	globals.uiData.setPosition(-400, 0);
	attribute1.uiData.setPosition(0, 0);
	// add1.uiData.setPosition(-200, 0);
}

export class EntityBuilderSopOnCreateRegister extends SopOnCreateHookRegister<SopType.ENTITY_BUILDER> {
	override type(): SopType.ENTITY_BUILDER {
		return SopType.ENTITY_BUILDER;
	}
	override onCreate(node: EntityBuilderSopNode) {
		onCreateHook(node);
	}
}
