/**
 *
 * it seems we cannot yet have this paramLabel
 * as it loads the node before the marker tracking is loaded,
 * which makes the menu entries in the params invalid
 *
 */

import {CameraWebXRARMarkerTrackingSopNode} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/sop/CameraWebXRARMarkerTracking';
import {
	MarkerTrackingSourceMode,
	// MARKER_TRACKING_SOURCE_MODES,
	// MARKER_TRACKING_TRANSFORM_MODES,
	// MarkerTrackingTransformMode,
} from '../../../../../../@polygonjs/polygonjs/src/core/webXR/markerTracking/Common';
import {
	paramLabelNumber,
	paramLabelString,
	paramLabelBoolean,
	ParamsLabelResult,
	ParamsLabelRegister,
} from '../BaseSetup';

export const cameraWebXRARMarkerTrackingSopParamsLabelRegister: ParamsLabelRegister<
	typeof CameraWebXRARMarkerTrackingSopNode,
	CameraWebXRARMarkerTrackingSopNode
> = {
	nodeClass: CameraWebXRARMarkerTrackingSopNode,
	setupFunc: (node) => ({
		params: node.params.all,
		callback: () => {
			const list: ParamsLabelResult[] = [paramLabelNumber(node.p.sourceMode)];

			if ([MarkerTrackingSourceMode.IMAGE, MarkerTrackingSourceMode.VIDEO].includes(node.sourceMode())) {
				paramLabelString(node.p.sourceUrl);
			}
			list.push(paramLabelNumber(node.p.transformMode));
			list.push(paramLabelBoolean(node.p.smooth));
			if (node.pv.smooth) {
				list.push(paramLabelNumber(node.p.smoothCount));
			}

			return list;
		},
	}),
};
